<template>
  <div class="boxcontent"  v-loading="loading"  element-loading-background="rgba(0, 0, 0, 0)" >
    <div  class="clearfix contionaryHeader">
      <el-button type="primary"  style="float: right" size="small" @click="cancelBtn"></el-button>
      <el-button type="primary"  style="float: right;;margin-right: 10px;" size="small" @click="saveBtn"></el-button>
    </div>
    <div>
      <table class="contionaryBox">
        <tr  v-for="(item,index) in entryTreeData" :key="item.id">
          <td style="width: 150px;">
            {{item.name}}
          </td>
          <td>
            <el-checkbox-group v-model="checkList"  @change="change">
              <el-checkbox v-for="(item1,index1) in item.children" :label="item1.id" :key="item1.id">{{item1.name}}</el-checkbox>
            </el-checkbox-group>
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
    export default {
      name: "entryDictionary",
      props:['personId','entryTree'],
      data(){
        return {
          entryTreeData:this.entryTree.entryTreeData,
          checkList:[],
          loading:false
        }
      },
      created(){
        this.getPerWordConditions()
      },
      methods:{
          change(){
            //
            console.log(this.checkList)
          },
          saveBtn(){
            this.loading = true
            //
            let checkList = JSON.stringify(this.checkList)
            let params = {
              personId:this.personId,
              personWordId:checkList
            }
            this.$http({
              url: this.$http.adornUrl(this.API.savePersonWord),
              method: 'post',
              data: this.$http.adornData(params)
            }).then(({data}) => {
              if(data && data.code===0){
                //
                this.$emit('getPerWordConditions')
                //
                this.$emit('dictionaryShow')
                this.loading = false
                this.$message({
                  message: '',
                  type: 'success'
                });
              }else{
                this.$message.error(data.msg);
              }
            });
          },
          getPerWordConditions(){
            //
            let This = this
            var params = {
              personId:this.personId
            }
            this.$http({
              url: this.$http.adornUrl(this.API.getPersonWordConditions),
              method: 'post',
              data: this.$http.adornData(params)
            }).then(({data}) => {
              if(data && data.code===0){
                let wordConditions = data.data
                if(wordConditions[0] !== null){
                  wordConditions.forEach(function(item,index){
                    This.checkList.push(item.wordId)
                  })
                }else{
                  this.$set(This.entryTree,'wordNames',[])
                  console.log(This.entryTree)
                }
              }else{
                this.$message.error(data.msg);
              }
            });
          },
          cancelBtn(){
           //
            this.$emit('dictionaryShow')
          }
      }
    }
</script>

<style lang="scss" scoped type="text/stylus">
 .boxcontent{
   width: 1000px;
   position: absolute;
   top: 100px;
   left: 15px;
   z-index: 100;
   background:#fff;
   box-shadow:0 2px 12px 0 rgba(0,0,0,.1);
   .contionaryHeader{
     margin:10px;
   }
   .contionaryBox{
      border-collapse: collapse;
      margin: 10px;
      td{
        border:1px solid #dedede;
        padding: 8px;
        text-align: left;
        line-height: 24px;
      }
   }
 }
</style>



