<template>
  <div class="clearfix">
    <el-select v-model="value" placeholder="" class="viewSelect" v-show="selectShow" @change="viewSelectChange">
      <el-option
        v-for="item in options"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-select>

    <div class="pagination-con">
      <el-pagination
        v-if="isSmall"
        small
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        layout="total,prev, pager, next"
        :page-size="page.pageSize"
        :total="page.total">
      </el-pagination>

      <el-pagination
         :page-sizes="[3, 10, 50, 100]"
         layout="total,sizes,prev, pager, next,jumper"
         :total="page.total"
         :page-size="page.pageSize"
         :current-page="page.pageIndex"
         @current-change="handleCurrentChange"
         @size-change="handleSizeChange">
      </el-pagination>

    </div>
  </div>
</template>
<script>
    export default {
        name:'pagination',//
        props:['page','isTotal','enableSmall','selectIsShow'],
        computed:{
            selectShow:
                function(){
                    if(this.selectIsShow == undefined){
                        return false
                    }else{
                        return true
                    }
                }
        },
        data() {
            return {
                isSmall:this.enableSmall || false,//small
                options: [],
                value: '4030C2A0-2FD5-4127-BB1B-F34E60F2BACF',
                records:[],
                selectedData:{}
            }
        },
        created(){
             this.getComponentsPage()
        },
        methods: {
            viewSelectChange(val){
              this.pickRoleName(val)
              this.$emit('viewChange',this.selectedData)
              console.log(this.selectedData)
            },
            pickRoleName(id){
              let obj = {};
              obj = this.records.find((item)=>{//userRoleList
                return item.componentsId === id;//
              });
              this.selectedData = obj;
            },
            handleCurrentChange(val){
                this.$emit('pageChange', val);
            },
            handleSizeChange(val) {
                console.log(` ${val} `);
                this.$emit('pageChangeSize', val);
            },
            getComponentsPage(){
                 //
              this.$http({
                url: this.$http.adornUrl(this.API.getComponentsPage),
                method: 'post',
                data: this.$http.adornData()
              }).then(({data}) => {
                if(data && data.code===0){

                   let optionArr =[]
                   this.records = data.data.records
                  this.records.forEach(function(item,index){
                     let obj ={
                       value:item.componentsId,
                       label:item.componentsName
                     }
                    optionArr.push(obj)
                  })
                  this.options = optionArr


                  this.pickRoleName(this.value)

                }else{
                  this.$message.error(data.msg);
                }
              });
            },
        }
    }
</script>
<style lang="scss">
  .pagination-con{
    height: 100%;
    float: right;
    .el-pagination{
      margin:0 !important;
      padding: 10px 10px 10px 0 !important;

    }
  }
  .viewSelect{
    margin-top: 7px;
    margin-left: 10px;
  }
  .clearfix:after{
    display:block;
    clear:both;
    content:"";
    visibility:hidden;
    height:0
  }
</style>



