<template>
  <el-dialog
    title=""
    width="600px"
    :close-on-click-modal="false"
    :visible.sync="visible"
    :append-to-body="true"
    :modal-append-to-body="false">
      <el-container>
        <el-upload
          class="upload-demo"
          drag
          action="https://jsonplaceholder.typicode.com/posts/"
          multiple>
          <i class="el-icon-upload"></i>
          <div class="el-upload__text"><em></em></div>
          <div class="el-upload__tip" slot="tip">jpg/png500kb</div>
        </el-upload>
      </el-container>
  </el-dialog>
</template>

<script>
  export default {
    name: "uploadAttachment",
    components:{},
    data (){
      return {
        visible:false,
      }
    },
    methods:{
      initDialog(){
        //
        this.visible = true
      },
    }
  }
</script>

<style lang="scss" scoped type="text/stylus">
  .el-container{
    align-items: center;
    justify-content: center;
    height: 300px;
  }

</style>



