<template>
  <el-dialog
    class="statisticalEcharts"
    title=""
    width="50%"
    top="30px"
    :close-on-click-modal="false"
    :visible.sync="visible">
    <el-container>
      <el-header>
        <el-row style="text-align: right;">
          <el-col>
            <el-button type="primary" @click="save"></el-button>
          </el-col>
        </el-row>
      </el-header>
      <el-main>


        <el-form class="commonsty-form" label-width="150px" >
          <el-row>
            <el-col >
              <el-form-item label="" prop="year">
                <el-checkbox-group v-model="formData.checkedyears">
                  <el-checkbox v-for="city in yearOptions" :label="city" :key="city">{{city}}</el-checkbox>
                </el-checkbox-group>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col >
              <el-form-item label="" prop="year">
                <el-tree-select
                  ref="eltreeSelect"
                  :props="props"
                  :options="options"
                  :clearable="isClearable"
                  :accordion="isAccordion"
                  v-model="formData.conclusion"/>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>




      </el-main>
    </el-container>
  </el-dialog>
</template>

<script>
    import elTreeSelect from "@/components/baseComponents/elTreeSelect";
    export default {
        name: "batchYearAssessment",
        components:{
          elTreeSelect
        },
        data () {
          return {
            visible:false,
            formData:{
              checkedyears:['2016', '2017'],
              conclusion:'0'
            },
            yearOptions: ['2016', '2017', '2018', '2019'],
            props:{
              value: 'itemCode',
              label: 'itemName',
              children: 'children',
              disabled:false
            },
            isClearable:false,      // 
            isAccordion:true,      // 
            valueId:'',            // ID
            options: [
              {
                id:"35ab8b9b9007418c9264d53fe65390f5",
                isParent:"false",
                itemCode:"0",
                itemName:"",
                open:false,
                pId:"-1"
              },
              {
                id:"a0d8523b29d34447861d2dd47e169249",
                isParent:"false",
                itemCode:"1",
                itemName:"",
                open:false,
                pId:"-1",

              }
            ]
          }
        },
        methods:{
          initDialog(){
            this.visible = true
          },
          save(){
            //
            console.log(this.formData)
          }
        }
    }
</script>

<style  lang="scss" scoped type="text/stylus">
  .el-container{
    height:338px;
  }
  .el-header{
    height: 44px!important;
    line-height: 44px;
  }
  .el-checkbox-group .el-checkbox:first-child
  {
    margin-left:20px;
  }
  .el-form-item__content .el-checkbox-group{
    line-height:32px;
  }
  >>> .el-input--medium .el-input__inner{
    border:none;
  }
  .commonsty-form .el-form-item{
    position:relative;
    min-height:38px;
  }
  .commonsty-form .el-form-item /deep/ .el-form-item__label{
    display:inline-block;
    height : 100%;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    padding-left: 6px;
    border-right: 1px solid #d1cece;
    background: #f9f9f9;
  }
</style>



