<template>
  <div style="height: 100%;">
   <ul class="popup-bottom clearfix" v-if="entryTree.wordNames.length !== 0">
     <li class="popup-bottom-list fleft"  v-for="(item,index) in entryTree.wordNames" :key="index">
      <span class="popup-bottom-style"></span>
       <span>{{item.wordName}}</span>
     </li>
   </ul>
   <div v-else class="nodatasty">
     
   </div>
  </div>
</template>

<script>
    export default {
        name: "informationFooter",
        props:['entryTree'],
        data() {
            return {

            }
        },
       updated(){
         console.log(this.entryTree)
       },
        created(){

        },
        methods: {

        }
    }
</script>

<style  lang="scss" scoped type="text/stylus">
  .popup-bottom {
    width:100%;
    margin:0;
    .fleft {
      float: left;
    }
    .popup-bottom-list {
      margin-right: 35px;
      line-height: 30px;
      min-width: 143px;
      cursor: default;

      .popup-bottom-style {
        display: inline-block;
        width: 12px;
        height: 12px;
        margin-right: 10px;
        background: url('../../../../assets/img/icon2.png') no-repeat -231px -164px;
      }
    }
  }
  .nodatasty{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    color: #909399;
  }
</style>



