<template>
  <div style="height: 100%" >
    <el-header>
      <el-row style="text-align: right;">
        <el-col>
          <template v-if="!isEdit">
            <!--<el-button type="primary" size="small" @click="importFile"></el-button>-->
            <div style="display: inline-block;" v-if="tabCode == 'A11'">
              <el-upload
                action=""
                :on-change="handleChange"
                accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.ms-excel"
                :auto-upload="false"
                :show-file-list=false>
                <el-button size="small" type="primary"></el-button>
              </el-upload>
            </div>
            <el-button type="primary" size="small" @click="copyHistory" v-if="tabCode == 'A02'"></el-button>
            <el-button type="primary" size="small" @click="dutySort" v-if="tabCode == 'A02'"></el-button>
            <el-button type="primary" size="small" @click="formAdd"></el-button>
            <el-button type="primary" size="small" @click="formEditBtn"  v-if="editBtnIsShow"></el-button>
            <el-button type="primary" size="small" @click="deleteTableItem" v-if="editBtnIsShow"></el-button>
          </template>
          <template v-else>
            <el-button type="primary" size="small" @click="saveEditData('editForm')"></el-button>
            <el-button type="primary" size="small" @click="cancelEdit"></el-button>
          </template>
        </el-col>
      </el-row>
    </el-header>
    <el-main class="grid-main" ref="gridmain" v-loading="loading" element-loading-background="rgba(255, 255, 255, 0)">
      <div>
        <el-row ref="formrow">
          <el-col>
            <el-main>
              <template v-if="!isEdit">
                <!---->
                <el-form class="commonsty-form echoForm" label-width="170px" label-position="right">
                  <el-row>
                    <el-col v-for="(item,index) in allData.form" :span="parseInt(item.colspan)" :key="index">
                      <el-form-item :label="item.colName" :prop="item.colCode">

                        <div v-if="item.controlType == '2'">
                          <el-input
                            type="textarea"
                            :rows="item.rowsNum"
                            v-model="formData[item.colCode]"
                            :disabled="true"
                            resize="none">
                          </el-input>
                        </div>
                        <div v-else>
                          <el-input v-model="formData[item.colCode]"  :disabled="true" ></el-input>
                        </div>

                      </el-form-item>
                    </el-col>
                  </el-row>
                </el-form>
              </template>
              <template v-else>
              <!-- -->
                <el-form class="commonsty-form editForm" :model="formEdit" ref="editForm" :rules="rules"  label-width="170px" label-position="right">
                  <el-row>
                    <el-col v-for="(item,index) in allData.form" :span="parseInt(item.colspan)" :key="index">
                      <el-form-item :label="item.colName" :prop="item.colCode">

                        <div v-if="showForm">
                          <!---->
                          <div v-if="item.controlType == '4' || item.controlType == '5'">
                            <el-tree-select
                              ref="eltreeSelect"
                              :props="props"
                              :options="dicCodeArr[item.colCode]"
                              :clearable="isClearable"
                              :accordion="isAccordion"
                              :disabled="item.isEdit == '1' && add == false"
                              v-model="formEdit[item.colCode]"/>
                          </div>
                          <!---->
                          <div v-if="item.controlType == '13'">
                            <el-tree-select
                              ref="eltreeSelect"
                              :props="props1"
                              :options="dicCodeArr[item.colCode]"
                              :clearable="isClearable"
                              :accordion="isAccordion"
                              :disabled="item.isEdit == '1' && add == false"
                              v-model="formEdit[item.colCode]"/>
                          </div>
                          <!---->
                          <div v-if="item.controlType == '15'">
                            <el-tree-select
                              ref="eltreeSelect"
                              :props="props1"
                              :multiple="multiple"
                              :options="arryearsOptions"
                              :clearable="isClearable"
                              :accordion="isAccordion"
                              :disabled="item.isEdit == '1' && add == false"
                              v-model="formEdit[item.colCode]"/>
                          </div>
                        </div>
                        <!---->
                        <div v-if="item.controlType == '3'">
                          <el-date-picker
                            v-model="formEdit[item.colCode]"
                            type="date"
                            placeholder=""
                            :disabled="item.isEdit == '1' && add == false"
                            @input="change">
                          </el-date-picker>
                        </div>
                        <!---->
                        <div v-if="item.controlType == '8'">
                          <el-input-number v-model="formEdit[item.colCode]" placeholder="" controls-position="right" :disabled="item.isEdit == '1' && add == false" style="width: 100%;"></el-input-number>
                        </div>
                        <!---->
                        <div v-if="item.controlType == '2'">
                          <el-input
                            type="textarea"
                            :rows="item.rowsNum"
                            placeholder=""
                            :disabled="item.isEdit == '1' && add == false"
                            v-model="formEdit[item.colCode]"
                            @input="change">
                          </el-input>
                        </div>
                        <!---->
                        <div v-if="item.controlType == '1'">
                          <el-input  v-model="formEdit[item.colCode]" :disabled="item.isEdit == '1' && add == false" @input="change"></el-input>
                        </div>

                      </el-form-item>
                    </el-col>
                  </el-row>
                </el-form>


              </template>

            </el-main>
          </el-col>
        </el-row>
        <el-row >
          <el-col>
            <el-main v-if="show" class="main-tablebox" :style="{'height':height,'min-height':'200px'}" >
              <el-table
                :data="tableData"
                size="small"
                style="width: 100%"
                height="100%"
                :row-class-name="tableRowClassName"
                highlight-current-row
                @row-click="rowClick"
                border
                @current-change="handleCurrentChange">
                <template   v-for="item in allData.cols">
                  <el-table-column
                    v-if="item.colCode == 'A0501A'"
                    :fixed="item.fixed"
                    :prop="item.colCode"
                    :label="item.colName"
                    :align="item.align == 0? 'left' : (item.align == 1 ? 'center' : 'right')"
                    :width="200"
                    :key="item.colCode"
                    :show-overflow-tooltip="true">
                    <template slot-scope="scope">
                      <el-button type="primary" plain size="small" @click="browse"></el-button>
                    </template>
                  </el-table-column>
                  <el-table-column
                    v-else
                    :fixed="item.fixed"
                    :prop="item.colCode"
                    :label="item.colName"
                    :align="item.align == 0? 'left' : (item.align == 1 ? 'center' : 'right')"
                    :width="200"
                    :key="item.colCode"
                    :show-overflow-tooltip="true">
                  </el-table-column>
                </template>

              </el-table>
            </el-main>
          </el-col>
        </el-row>
      </div>
    </el-main>

    <copy-history ref="copyHistory"></copy-history>
    <duty-sort ref="dutySort" :allData="allData" @getJobInformation="getJobInformation"></duty-sort>
    <file-browsing ref="fileBrowsing"></file-browsing>
    <!--<import-file ref="importFile"></import-file>-->
  </div>
</template>

<script>
  import copyHistory from './informationTools/copyHistory'
  import dutySort from './informationTools/dutySort'
  import elTreeSelect from "@/components/baseComponents/elTreeSelect"
  import fileBrowsing from './informationTools/fileBrowsing'
  import importFile from './informationTools/importFile'
  import {arrYear} from "@/utils/index";
  export default {
    name: "informationGrid",
    components:{copyHistory,dutySort,elTreeSelect,fileBrowsing,importFile},
    props:['personId','currMenu'],
    data() {
      return {
        multiple:true,
        loading:false,
        add:false,
        editBtnIsShow:true,
        showForm:true,
        show:false,
        height:'',
        isEdit:false,
        props:{                // 
          value: 'itemCode',
          label: 'itemName',
          children: 'children',
          disabled:true
        },
        props1:{
          value: 'id',
          label: 'name',
          children: 'children',
          disabled:true
        },
        isClearable:true,      // 
        isAccordion:true,      // 
        valueId:'',            // ID
        currentRowIndex:0,
        currentRow:{},
        necessaryFormData:[],
        rules: {},
        tableRowClassName({row, rowIndex}) {
          row.rowIndex = rowIndex;
        },
        allData:{},
        tableData:[],//
        formData:{},//
        formEdit:{},//form
        dicCodeArr:{},
        counter:0,
        tabCode:'',
        dataTest:{
          "code": 0,
          "msg": "success",
          "data": {
            "data": [{
              "B0523": "02",
              "B0524": "2020-06-02",
              "B0525": "21",
              "B0526": 1,
              "B0514B": "2",
              "formateddata": {
                "B0525": "",
                "B0526": "",
                "B0514B": "",
                "B0504A": "",
                "B0511": "",
                "B0522":''
              },
              "A0100": "0EAC97EF-A507-48B8-92F7-98CCE9F8CDCE",
              "B0514A": "www555",
              "B0504A": "025",
              "B0500": "1E404A64-8211-4F05-9C3A-7BCFFA9AD9F0",
              "B0511": "21",
              "B0522": "30",
            }, {
              "B0523": "02",
              "B0524": "2020-06-02",
              "B0525": "21",
              "B0526": 1,
              "B0514B": "2",
              "formateddata": {
                "B0525": "",
                "B0526": "",
                "B0514B": "",
                "B0504A": "",
                "B0511": "",
                "B0522":'sssssss'
              },
              "A0100": "0EAC97EF-A507-48B8-92F7-98CCE9F8CDCE",
              "B0514A": "www555",
              "B0504A": "01",
              "B0500": "1E404A64-8211-4F05-9C3A-7BCFFA9AD9F0",
              "B0511": "21",
              "B0522": "30"
            }],
            "form": [{
              "colspan": "8",
              "colName": "",
              "controlType": "4",
              "dicCode": "ZB20",
              "colCode": "B0511"
            }, {
              "colspan": "8",
              "colName": "",
              "controlType": "1",
              "dicCode": "XZ09",
              "colCode": "B0514A"
            }, {
              "colspan": "8",
              "colName": "",
              "controlType": "4",
              "dicCode": "ZB19",
              "colCode": "B0514B"
            }, {
              "colspan": "8",
              "colName": "",
              "controlType": "4",
              "dicCode": "ZB65",
              "colCode": "B0504A"
            }, {
              "colspan": "8",
              "colName": "",
              "controlType": "4",
              "dicCode": "HXDICT",
              "colCode": "B0523"
            }, {
              "colspan": "8",
              "colName": "",
              "controlType": "3",
              "dicCode": null,
              "colCode": "B0524"
            }, {
              "colspan": "8",
              "colName": "",
              "controlType": "4",
              "dicCode": "ZB20",
              "colCode": "B0525"
            }, {
              "colspan": "8",
              "colName": "",
              "controlType": "1",
              "dicCode": "subType",
              "colCode": "B0526"
            }],
            "cols": [{
              "align": "2",
              "colCode": "B0501",
              "colName": "",
              "controlType": "4",
              "sortAble": "0",
              "width": "100",
              "dicCode": null
            }, {
              "align": "2",
              "colCode": "B0504",
              "colName": "",
              "controlType": "1",
              "sortAble": "0",
              "width": "100",
              "dicCode": null
            }, {
              "align": "2",
              "colCode": "B0511",
              "colName": "",
              "controlType": "4",
              "sortAble": "0",
              "width": "100",
              "dicCode": "ZB20"
            }, {
              "align": "2",
              "colCode": "B0514",
              "colName": "",
              "controlType": "1",
              "sortAble": "0",
              "width": "100",
              "dicCode": null
            }, {
              "align": "2",
              "colCode": "B0504A",
              "colName": "",
              "controlType": "4",
              "sortAble": "0",
              "width": "100",
              "dicCode": "ZB65"
            }, {
              "align": "2",
              "colCode": "B0522",
              "colName": "",
              "controlType": "1",
              "sortAble": "0",
              "width": "100",
              "dicCode": null
            }, {
              "align": "2",
              "colCode": "B0524",
              "colName": "",
              "controlType": "3",
              "sortAble": "0",
              "width": "100",
              "dicCode": null
            }]
          },
          "total": 0
        },
        formLabel:{},
        arryearsOptions:[],
        fileTemp:null
      }
    },
    created(){
      this.getJobInformation()

      //
      let This = this
      let arryears = arrYear(1996)
      arryears.forEach(function(item,index){
        let obj = {
          id:item,
          name:item,
          children: [],
        }
        This.arryearsOptions.push(obj)
      })
      console.log(This.arryearsOptions)
    },
    watch:{
      personId(){
        this.getJobInformation()
      },
      currMenu(newName, oldName){
        this.getJobInformation()
      },
    },
    methods: {
      change(){
        this.$forceUpdate()
      },
      getJobInformation(){
        //
        this.loading = true
        this.currentRowIndex = 0

        this.dicCodeArr = {}
        this.counter = 0

        //
        this.isEdit = false
        this.props.disabled = true
        this.props1.disabled = true


        let objIsO = (JSON.stringify(this.currMenu) == "{}");
        if(objIsO){
          this.tabCode = 'A14Z1'
        }else{
          this.tabCode = this.currMenu.code
        }
        var params = {
          personId:this.personId,
          tabCode:this.tabCode
        }

        this.$http({
          url: this.$http.adornUrl(this.API.getJobInformation),
          method: 'post',
          data: this.$http.adornData(params)
        }).then(({data}) => {
          this.loading = false

          if(data && data.code===0){
            //
            if(data.data.data.length != 0){
              //0
              // this.allData = JSON.parse(JSON.stringify(this.dataTest.data))
              this.allData = JSON.parse(JSON.stringify(data.data))
              this.formLabel = this.allData.form
              //
              this.editBtnIsShow = true


              //  -- form--
              let dataArr = JSON.parse(JSON.stringify(this.allData.data))
              let arr = []
              dataArr.forEach(function(item,index){
                for(var key0 in item){
                  let formateddata = item.formateddata
                  for(var key in formateddata){
                    if(key == key0){
                      item[key0] = formateddata[key]
                    }
                  }
                }
                delete item.formateddata
                arr.push(item)
              })
              this.tableData = arr
              //formTable
              this.formData = this.tableData[this.currentRowIndex]
              console.log(this.formData)

            }else{
              //0
              this.editBtnIsShow = false

              // this.allData = JSON.parse(JSON.stringify(this.dataTest.data))
              this.allData = JSON.parse(JSON.stringify(data.data))
              this.tableData = this.allData.data

              //0
              let obj ={}
              this.allData.form.forEach(function(item,index){
                obj[item.colCode] = ''
              })
              this.formData = obj
            }


            if(this.allData.cols.length == 0 || this.tableData.length == 0){
              this.show = false
            }else{
              this.show = true
            }
            //rules
            let formColCodeArr= this.allData.form
            this.necessaryFormData = formColCodeArr.filter( item=> item.colIsNotNull == '1')
            let rulesObj = {}
            this.necessaryFormData.forEach(function(item,index){
              rulesObj[item.colCode] =  { required: true, message: '', trigger: 'blur' }
            })
            this.rules = rulesObj

            //
            let dicArr =   this.allData.form.filter(item => item.controlType === '4' || item.controlType === '13' || item.controlType === '5')
            let This = this
            let dicArrLength = dicArr.length
            //form
            if(dicArrLength == 0){
              this.showForm = true
            }else{
              dicArr.forEach(function(item,index){
                //
                This.getDicCodeItem(item,dicArrLength)
              })
            }

            this.$nextTick(function(){
              if(this.showForm){
                //
                let allheight = this.$refs.gridmain.$el.offsetHeight
                let height= this.$refs.formrow.$el.offsetHeight;
                // 9 main-tablebox margin  
                let temp = allheight - height - 9
                this.height = temp + 'px'
              }
            })
          }else{
            this.$message.error(data.msg);
          }
        });
      },
      getDicCodeItem(item,dicArrLength){
        let This = this
        //
        let dicCode = item.dicCode
        let controlType = item.controlType
        var params = {
          dictCode:dicCode
        }

        if(controlType == '13'){
          this.$http({
            url: this.$http.adornUrl(this.API.getOrganizationTree),
            method: 'post',
            data: this.$http.adornData(params)
          }).then(({data}) => {
            if(data && data.code===0){
              this.dicCodeArr[item.colCode]=data.data
                this.showForm = true
            }else{
              this.$message.error(data.msg);
            }
          });
        }else{
          this.$http({
            url: this.$http.adornUrl(this.API.getDictItemByCode),
            method: 'post',
            data: this.$http.adornData(params)
          }).then(({data}) => {
            if(data && data.code===0){
              this.dicCodeArr[item.colCode]=data.data
                this.showForm = true
            }else{
              this.$message.error(data.msg);
            }
          });
        }
      },
      browse(){
        //
        this.$refs.fileBrowsing.initDialog()
      },
      copyHistory(){
        //
        this.$refs.copyHistory.initDialog()
      },
      importFile(){
        //xlsx
        this.$refs.importFile.initDialog()
      },
      handleChange(file, fileList){

        this.fileTemp = file.raw

        if(this.fileTemp){
          if((this.fileTemp.type == 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet') || (this.fileTemp.type == 'application/vnd.ms-excel')){

            var formData = new FormData()
            if(file !== undefined){
              formData.append("file", file.raw);
              formData.append('a0100',this.personId)
            }
            this.$http({
              headers: {
                'Content-Type': 'multipart/form-data;'
              },
              url: this.$http.adornUrl(this.API.importExcel),
              method: 'post',
              data: formData
            }).then(({data}) => {
              if(data && data.code===0){
                this.getJobInformation()
                this.$message({
                  message: '',
                  type: 'success'
                });
              }else{
                this.$message.error(data.msg);
              }
            })

          } else {
            this.$message({
              type:'warning',
              message:' xlsx / xls '
            })
          }
        } else {
          this.$message({
            type:'warning',
            message:''
          })
        }
      },
      dutySort(){
        //
        this.$refs.dutySort.initDialog()
      },
      formAdd(){
        //
        this.isEdit = true
        this.add = true
        this.props.disabled = false
        this.props1.disabled = false
        //input

        let editData = JSON.parse(JSON.stringify(this.allData.form))
        let formEditObj = {}
        editData.forEach(function(item,index){
          formEditObj[item.colCode] = ''
        })
        this.formEdit = Object.assign({}, formEditObj)

      },
      formEditBtn(){
        //
        this.isEdit = true
        this.add = false
        this.props.disabled = false
        this.props1.disabled = false
        //
        if(this.allData.data.length !== 0){
          let editData = this.allData.data[this.currentRowIndex]
          delete editData.formateddata

          let formEditObj = {}
          this.formLabel.forEach(function(item,index){
            for(var key in editData){
              if(item.colCode == key){
                formEditObj[item.colCode] = editData[key]
              }
            }
          })

          this.formEdit = Object.assign({}, formEditObj)
        }


      },
      saveEditData(formName){
        //
        let This = this

        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.submitForm()
          } else {
            //
            let necessaryArrData =[]
            this.necessaryFormData.forEach(function(item,index){
              // for in chrome opera
              for(let key in This.formEdit){
                if(item.colCode == key){
                  if(This.formEdit[key] == '' || This.formEdit[key] == null){
                    let colNameShow = item.colName
                    necessaryArrData.push(colNameShow)
                  }else{
                    return false
                  }
                }
              }
            })
            if(necessaryArrData.length != 0){
              let msgStr =''
              necessaryArrData.forEach(function(item,index){
                let  strItem = `<div style="display:flex;align-items:center;margin-bottom:4px;"><i class="el-icon-warning-outline iconsty" style="color:#E5463A;font-size:20px;"></i><span style="font-size:16px;"><span style="color: #498ef7;">${item}</span></span></div>`
                msgStr += strItem
              })
              This.$alert(msgStr, '', {
                dangerouslyUseHTMLString: true
              });
            }

            return false;
          }
        });
      },
      submitForm(){
        let a = []
        a.push(this.formEdit)

        //add  true fasle 
        let  unA01Data = {
          a0100:this.personId,
          cols:a,
          tabCode:this.tabCode
        }
        let url = ''

        if(!this.add){
          //
          let idname = this.tabCode + '00'
          let id = this.allData.data[this.currentRowIndex][idname]
          unA01Data.id = id
          url = this.$http.adornUrl(this.API.updateJobInformation)
        }else{
          //
          url = this.$http.adornUrl(this.API.saveJobInformation)
        }

        //
        let dataList = []
        let obj
        let editObj = Object.assign({},this.formEdit)
        for(var key in  editObj)
        {
          if(Array.isArray(editObj[key])){
            editObj[key].forEach(function(item,index){
              obj ={
                [key]:item
              }
              dataList.push(obj)
            })
            delete  editObj[key]
          }
        }
        let arr = []
        dataList.forEach(function(item,index){
          let item1 = Object.assign(item,editObj)
          arr.push(item1)
        })
        for(var key in  this.formEdit)
        {
          if(Array.isArray(this.formEdit[key])){
            unA01Data.cols = arr
          }
        }

        //
        this.$http({
          headers: {
            'Content-Type': 'application/json;charset=UTF-8'
          },
          url: url,
          method: 'post',
          data: unA01Data
        }).then(({data}) => {
          console.log(data)
          if(data && data.code===0){
            //
            this.isEdit = false
            this.getJobInformation()
            this.$message({
              message: '',
              type: 'success'
            });
          }else{
            this.$message.error(data.msg);
          }
        });
      },
      cancelEdit(){
        //
        this.isEdit = false
        this.props.disabled = true
        this.props1.disabled = true
        // this.getJobInformation()
      },
      deleteTableItem(){
        //
        let idname = this.tabCode + '00'
        let id = this.allData.data[this.currentRowIndex][idname]
        let param  = {
          tabCode:this.tabCode,
          id:id
        }
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {

          this.$http({
            headers: {
              'Content-Type': 'application/json;charset=UTF-8'
            },
            url: this.$http.adornUrl(this.API.deleteJobInformation),
            method: 'post',
            data: param
          }).then(({data}) => {
            console.log(data)
            if(data && data.code===0){
              //
              this.isEdit = false
              this.getJobInformation()

            }else{
              this.$message.error(data.msg);
            }
          });

          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '!'
          });
        });
      },
      rowClick(row, event, column){
        //
        if(this.isEdit){
          this.isEdit = false
          this.props.disabled = true
          this.props1.disabled = true
        }
        this.currentRowIndex = row.rowIndex
        this.formData = this.tableData[row.rowIndex]

        console.log(row.rowIndex)
        this.formEdit = Object.assign({}, this.allData.data[row.rowIndex])
        console.log(this.formEdit)
      },
      handleCurrentChange(val){
        this.currentRow = val
      },

    },
    computed:{
    },

  }
</script>

<style lang="scss" scoped type="text/stylus">
  .el-header{
    height: 44px!important;
    line-height: 44px;
  }
  .el-main{
    padding: 0;
    .main-table{
      width: 100%;
      tbody {
        tr{
          height: 30px;
          td{
            border: 1px solid red;
            .el-form-item{
              margin-bottom: 0;
            }
          }
        }
      }
    }
  }


  .el-input--medium.is-disabled{
    /*background:#FFFFF3;*/
  }
  .grid-main{
    padding: 0 16px;
    position: absolute;
    top: 44px;
    left: 0;
    right: 0;
    bottom: 0;
  }
  /**/
  .main-tablebox{
    margin:0 4px 8px 4px;
    /*border: 1px solid #d1cece;*/
    border:none;
  }
  >>> .el-table--border, .el-table--group {
    border: 1px solid #d1cece;
  }
  >>> .el-table--border {
    border-right: none;
    border-bottom: none;
  }
  >>> .el-table td, .el-table th.is-leaf {
    border-bottom: 1px solid #d1cece;
  }
  >>> .el-table--border::after, .el-table--group::after, .el-table::before {
    background-color: #d1cece;
  }
  //input
  .el-input.is-disabled .el-input__inner{
    background:none;
    border:none;
  }
  .el-select .el-input.is-disabled .el-input__inner{
    background:none;
    border:none;
  }
  >>> .el-textarea.is-disabled .el-textarea__inner,>>> .el-textarea__inner{
    background: #fff;
    border:none;
  }
  .commonsty-form .el-form-item{
    position:relative;
    min-height:38px;
  }
  .commonsty-form .el-form-item /deep/ .el-form-item__label{
    display:inline-block;
    height : 100%;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    padding-left: 6px;
    border-right: 1px solid #d1cece;
    background: #f9f9f9;
  }
  >>>.el-input-number .el-input__inner{
    text-align:left;
  }
  //  
  >>> .el-form-item__error{
    display:none;
  }


</style>



