<template>
  <el-dialog
    title=""
    width="600px"
    :close-on-click-modal="false"
    :visible.sync="visible"
    :append-to-body="true"
    :modal-append-to-body="false">
    <el-container>
      <el-upload
        class="upload-demo"
        action=""
        :on-change="handleChange"
        :on-exceed="handleExceed"
        accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.ms-excel"
        :auto-upload="false">
        <el-button size="small" type="primary"></el-button>
      </el-upload>
    </el-container>
  </el-dialog>
</template>

<script>
  export default {
    name: "importFile",
    components:{},
    data (){
      return {
        visible:false,
      }
    },
    methods:{
      initDialog(){
        //
        this.visible = true
      },
      handleChange(file, fileList){
        console.log(file, fileList)
        this.fileTemp = file.raw

        if(this.fileTemp){
          if((this.fileTemp.type == 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet') || (this.fileTemp.type == 'application/vnd.ms-excel')){
            this.importfxx(this.fileTemp)
          } else {
            this.$message({
              type:'warning',
              message:' xlsx / xls '
            })
          }
        } else {
          this.$message({
            type:'warning',
            message:''
          })
        }
      },

      handleRemove(file,fileList){
        this.fileTemp = null
      },
      importfxx(obj) {
        let _this = this;
        // DOM
        this.file = obj
        var rABS = false; //
        var f = this.file;
        var reader = new FileReader();
        //if (!FileReader.prototype.readAsBinaryString) {
        FileReader.prototype.readAsBinaryString = function(f) {
          var binary = "";
          var rABS = false; //
          var pt = this;
          var wb; //
          var outdata;
          var reader = new FileReader();
          reader.onload = function(e) {
            var bytes = new Uint8Array(reader.result);
            var length = bytes.byteLength;
            for(var i = 0; i < length; i++) {
              binary += String.fromCharCode(bytes[i]);
            }
            var XLSX = require('xlsx');
            if(rABS) {
              wb = XLSX.read(btoa(fixdata(binary)), { //
                type: 'base64'
              });
            } else {
              wb = XLSX.read(binary, {
                type: 'binary'
              });
            }
            outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]);//outdata
            console.log(outdata)
            this.da = [...outdata]
            let arr = []
            this.da.map(v => {
              console.log(v)
              let obj = {}
              obj.pxcbdw = v.pxcbdw
              obj.pxjsrq = v.pxjsrq
              obj.pxlb = v.pxlb
              obj.pxlgzt = v.pxlgzt
              obj.pxqzrq = v.pxqzrq
              obj.pxsc = v.pxsc
              obj.pxzbdwjb = v.pxzbdwjb
              obj.zbdw = v.zbdw
              obj.zdlypxmc = v.zdlypxmc
              arr.push(obj)
            })
            console.log(arr)
            return arr
          }
          reader.readAsArrayBuffer(f);
        }

        if(rABS) {
          reader.readAsArrayBuffer(f);
        } else {
          reader.readAsBinaryString(f);
        }
      },
      handleExceed(files, fileList) {
        console.log(files, fileList)
        this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
      },
    }
  }
</script>

<style lang="scss" scoped type="text/stylus">
  .el-container{
    align-items: center;
    justify-content: center;
    height: 300px;
  }

</style>



