<template>
  <el-dialog class="statisticalEcharts" title="" width="900px" top="30px" :close-on-click-modal="false" :visible.sync="visible">

    <el-tabs type="border-card" @tab-click="tabClick" v-model="tabName">
      <el-tab-pane label="" name="0" v-if="selectedData.componentsType !== '1'">
        <el-container>
          <el-header>
            <el-row>
              <el-col>
                <el-button type="primary" size="small" @click="save"></el-button>
              </el-col>
            </el-row>
          </el-header>
          <el-main style="height: 523px;" v-loading="loading">
            <warmer-transfer v-if="show&&show1" :sourcedata="sourcedata" sourcetitle="" :targetdata="targetdata" targettitle="" @getTargetList="getTargetList"></warmer-transfer>
          </el-main>
        </el-container>
      </el-tab-pane>
      <el-tab-pane label="" name="1">
        <column-advanced-setup ref="columnAdvancedSetup" :checkedData="selectedData" @getDataList="getDataList" @closeDialog="closeDialog" @updateFn="updateFn"></column-advanced-setup>
      </el-tab-pane>
    </el-tabs>

  </el-dialog>
</template>

<script>
import { swapItems } from '@/utils';
import warmerTransfer from '@/components/baseComponents/warmerTransfer'
import columnAdvancedSetup from '@/components/baseComponents/columnAdvancedSetup'
export default {
  name: "setDisplay",
  props: ['selectedData'],
  components: {
    warmerTransfer, columnAdvancedSetup
  },
  data () {
    return {
      tabName: '0',
      loading: true,
      show: false,
      show1: false,
      targetdata: [],
      visible: false,
      sourcedata: [],
      value: [],
      rightData: [],
      props: {
        key: 'colId',
        label: 'listTitle'
      },
      columnData: [],
      rightCheckedData: [],
      val: [],
      checkedData: {
        componentsDesc: "",
        componentsId: "4030C2A0-2FD5-4127-BB1B-F34E60F2BACF",
        componentsName: "",
        componentsType: "0",
        createtime: "2020-04-28 11:36:12",
        createuser: "admin",
        modifytime: "2020-05-29 11:47:19",
        modifyuser: "admin",
        orderno: 1,
        tabId: "0416DD56-CFC4-4CDD-8177-015E7C874AD6"
      }
    }
  },
  created () {
    this.getDataList()
  },
  methods: {
    updateFn () {
      this.$emit('getDataList')
    },
    initDialog () {
      // this.tabName = '0'
      if (this.selectedData.componentsType == '1') {
        this.tabName = '1'
      } else {
        this.getDataList()
        this.tabName = '0'
      }

      this.visible = true
    },
    getTargetList (val) {
      //transfer
      this.val = val
    },
    getDataList () {
      //
      let params = {
        tabId: this.selectedData.tabId
      }
      this.$http({
        url: this.$http.adornUrl(this.API.getDataTableColumn),
        method: 'post',
        data: this.$http.adornData(params)
      }).then(({ data }) => {
        if (data && data.code === 0) {
          //
          this.columnData = data.data
          let dataArr = []
          this.columnData.forEach(function (item, index) {
            dataArr.push({
              id: item.colId,
              name: item.colName,
              isSelected: false
            });
          })
          this.sourcedata = dataArr
          this.loading = false
          this.show = true
        } else {
          this.$message.error(data.msg);
        }
      });

      //value
      let param = {
        componentsId: this.selectedData.componentsId,
        listFlog: '1'
      }
      this.$http({
        url: this.$http.adornUrl(this.API.getDataComponentsList),
        method: 'post',
        data: this.$http.adornData(param)
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.value = []
          this.rightData = data.data
          let dataArr = []
          this.rightData.forEach(function (item, index) {
            dataArr.push({
              id: item.colId,
              name: item.colName,
              isSelected: false
            });
          })

          this.targetdata = dataArr
          this.show1 = true

        } else {
          this.$message.error(data.msg);
        }

      });
    },
    rightCheckChange (val) {
      //
      let arrdata = []
      val.map(id => {
        let obj = {}
        obj = this.rightData.find((item) => {
          return item.colId == id
        })
        arrdata.push(obj)
      })
      this.rightCheckedData = arrdata

    },
    save () {
      console.log(this.targetdata)
      //
      // valueid
      let arrdata = []
      this.val.map(val => {
        let obj = {}
        obj = this.columnData.find((item) => {
          console.log(item.colId)
          return item.colId == val.id
        })
        arrdata.push(obj)
      })
      this.rightData = arrdata
      //
      let arr = []
      this.rightData.forEach(function (item, index) {
        let obj = {
          listTitle: item.colName,
          listAlign: 'center',
          listVertical: 'center',
          listIsorder: '1',
          listWidth: 200,
          colName: item.colName,
          colCode: item.colCode,
          colId: item.colId,
        }
        arr.push(obj)
      })

      this.$http({
        url: this.$http.adornUrl(this.API.addDataComponentsList),
        method: 'post',
        data: this.$http.adornData({
          data: JSON.stringify(arr),
          id: this.selectedData.componentsId,
          code: '1'
        })
      }).then(({ data }) => {
        if (data.code === 0) {
          this.$message.success('');
          this.$emit('getDataList')
          this.visible = false
        } else {
          this.$message.error(data.msg);
        }
      })
    },
    tabClick () {
      //tab tab
      this.$refs.columnAdvancedSetup.getTableData()
    },
    closeDialog () {
      this.visible = false
      this.$emit('getDataList')
    }
  }
}
</script>

<style lang="scss" scoped>
.el-header {
  height: 36px !important;
  padding: 0;
}
.el-header .el-button {
  float: right;
}
>>> .el-transfer-panel {
  width: 317px;
  height: 500px;
}
>>> .el-transfer-panel__list.is-filterable {
  height: 400px;
}

>>> .el-transfer__buttons {
  position: relative;
  height: 82px;
}
>>> .el-transfer__buttons .el-transfer__button:first-child {
  position: absolute;
  bottom: 0;
  margin-bottom: 0;
}
>>> .el-transfer-panel__footer {
  text-align: center;
  .el-button {
    margin-top: 4px;
  }
  .el-button:first-child {
    margin-left: 15px;
  }
}
.el-main {
  padding-top: 0;
}
</style>



