<script>
    import $ from 'jquery'
    export default {
        name: "appointApproveTable",
        props:['personId'],
        data() {
            return {
                visible:false,
                src:require("@/assets/img/no_pic.jpg"),
                data:{},
                isShow:false,
                noPic:require("@/assets/img/no_pic.jpg"),
                href:'',
                tokenstr:'',
                jtcysl:null,
                loading:false

            }
        },
        watch:{
          personId:{
            handler(){
              this.href = `${this.$host}${this.API.rmExportWords}?token=${this.tokenstr}&a0100=${this.personId}&state=1`
            }
          }
        },
        created(){
          this.getDataList()
          this.token =  sessionStorage.getItem('token')
        },
        methods: {
            initDialog(){
                //
                this.getDataList()
                this.visible = true
            },
            getDataList(){
              this.isShow = false
              this.loading = true
              let params = {
                a0100:this.personId
              }
              this.$http({
                url: this.$http.adornUrl(this.API.rmTableInformation),
                method: 'post',
                data: this.$http.adornData(params)
              }).then(({data}) =>{
                if(data.code === 0) {
                  //
                  this.data = data.data
                  //
                  if(this.data.zhaoPianPath !== ''){
                    let token = this.token
                    this.tokenstr= token.replace("\"","").replace("\"","")
                    this.src = `${this.$host}${this.data.zhaoPianPath}?token=${this.tokenstr}`
                    //
                    this.href = `${this.$host}${this.API.rmExportWords}?token=${this.tokenstr}&a0100=${this.personId}&state=1`
                  }else{
                    this.src = this.noPic
                  }

                  //
                  if(this.data.jiaTingChengYuanList == null){
                    this.data.jiaTingChengYuanList = []
                  }
                  this.jtcysl = 7 - this.data.jiaTingChengYuanList.length
                  this.isShow = true
                  this.loading = false

                } else {
                  this.$message.error(data.msg);
                }
              })

            },
            ExportWord(val){
              let url = ''
              if(val == 'A4'){
                  url = this.API.rmExportWords
              }else if(val == 'Lrmx'){
                url = this.API.rmTableExportLrmx
              }
              //
              this.token = this.token.replace("\"","").replace("\"","")
              let $eleForm = $(`<form method='post' ><input  name='a0100' value=${this.personId}><input  name='state' value='1'><input  name='token' value='${this.token}'></form>`);
              $eleForm.attr("action",`${this.$host}${url}`);
              $('.table-container').append($eleForm);
              //
              $eleForm.submit();

            },
        }
    }
</script>

