<template>
    <div class="box">
        <el-container style="height: 100%;">
           <el-aside width="20%" >           
                <el-scrollbar style='height: 100%;'>
                    <metadataTree ref="moduleTree" :treeData="treeData" @node-click="orgTreeClick" style="margin-top:10px;"></metadataTree>
                </el-scrollbar>
            </el-aside>
            <el-main>
                <div class='top-btn'>
                    <el-button type="primary" size="small" @click="moveDirection('up')"></el-button>
                    <el-button type="primary" size="small" @click="moveDirection('down')"></el-button>
                </div>
                <el-table
                    :data="tableData"
                    @selection-change="selectionChangeHandle"
                >
                    <el-table-column type="selection" width="50" align="center"></el-table-column>
                    <el-table-column prop="expressionPrefix" label="">
                        <template slot-scope="scope">
                            <el-select v-model="scope.row.expressionPrefix" placeholder="" clearable>
                                <el-option
                                    v-for="item in expressionPrefix"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.label"
                                >
                                </el-option>
                            </el-select>
                        </template>
                    </el-table-column> 
                    <el-table-column prop="name" label="">
                        <template slot-scope="scope">
                            <el-input v-model="scope.row.name" placeholder=""></el-input>
                        </template>
                    </el-table-column>
                    <el-table-column prop="operatorShow" label="">
                        <template slot-scope="scope">
                            <el-select v-model="scope.row.operatorShow" placeholder="" clearable>
                                <el-option
                                    v-for="item in operatorShow"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.label"
                                >
                                </el-option>
                            </el-select>
                        </template>
                    </el-table-column> 
                    <el-table-column prop="valueShow" label="">
                        <template slot-scope="scope">
                            <el-select v-model="scope.row.valueShow" placeholder="" clearable>
                                <el-option
                                    v-for="item in valueShow"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.label"
                                >
                                </el-option>
                            </el-select>
                        </template>
                    </el-table-column> 
                    <el-table-column prop="expressionSuffix" label="">
                        <template slot-scope="scope">
                            <el-select v-model="scope.row.expressionSuffix" placeholder="" clearable>
                                <el-option
                                    v-for="item in expressionSuffix"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.label"
                                >
                                </el-option>
                            </el-select>
                        </template>
                    </el-table-column> 
                    <el-table-column prop="connectorShow" label="">
                        <template slot-scope="scope">
                            <el-select v-model="scope.row.connectorShow" placeholder="" clearable>
                                <el-option
                                    v-for="item in connectorShow"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.label"
                                >
                                </el-option>
                            </el-select>
                        </template>
                    </el-table-column> 
                    <el-table-column label=""  width="100">
                        <template slot-scope="scope">
                            <el-button type="danger" size="mini" plain @click="handleDelete(scope.$index, scope.row)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div>
                    <el-form ref="addForm" :model="dataForm" label-width="150px">
                         <el-row>
                            <el-col :span="22">
                                <el-form-item label="" prop="conditionStatus">
                                    <el-radio-group v-model="dataForm.conditionStatus">
                                        <el-radio :label="0"></el-radio>
                                        <el-radio :label="1"></el-radio>
                                    </el-radio-group>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="22">
                                <el-form-item label="" prop="conditionDesc">
                                    <el-input type="textarea" :rows="2" v-model="dataForm.conditionDesc"></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <div class="dialog-footer">
                            <el-button type="primary" size="small" @click="saveClick()"></el-button>
                            <el-button type="primary" size="small" @click="describeClick()"></el-button>
                        </div>
                    </el-form>
                </div>
           </el-main>
       </el-container>
    </div>
</template>

<script>
    import {swapItems} from '@/utils';
    import metadataTree from "@/components/baseComponents/orgTree.vue";
    export default{
        props: ['checkedData','isEdit'],
        components: {  
            metadataTree,
        },
        data(){
            return{
                expressionPrefix:this.API.constObj.expressionPrefix,
                expressionSuffix:this.API.constObj.expressionSuffix,
                operatorShow:this.API.constObj.operatorShow,
                connectorShow:[
                    {
                        value: '1',
                        label: ''
                    }, 
                    {
                        value: '2',
                        label: ''
                    }, 
                ],
                valueShow:[
                    {
                        value: '1',
                        label: ''
                    }, 
                    {
                        value: '2',
                        label: ''
                    }, 
                ],
                treeData:[],
                tableData: [],
                currentRow:'',
                dataForm:{
                    conditionStatus:1, 
                    conditionDesc:'',
                },
                conditionId:'',//id
            }
        },
        created(){ 
           this.getDataTree();
        },
        methods:{
            //
            getDataTree(){
                this.$http({
                    url: this.$http.adornUrl(this.API.getTableColumnTree),
                    method: 'post',
                    data: this.$http.adornData({})
                }).then(({data}) => {
                    if(data && data.code===0){
                        this.treeData=data.data;
                        // this.orgTreeClick(this.treeData[0])
                    }else{
                        this.$message.error(data.msg);
                        this.treeData=[];
                    }
                });
            },
            orgTreeClick(data){
                this.currentRow = data;
                this.isParent = data.isParent;
                if(this.isParent === false){
                     var list = {
                        name: data.name,
                        connectorShow:'',
                        operatorShow:'',
                    };
                    this.tableData.push(list);
                }
            },
            //
            getDataList(row){
                this.conditionId = row.conditionId;
                this.$http({
                    url: this.$http.adornUrl(this.API.queryPersonMingceCondition),
                    method: 'post',
                    data: this.$http.adornData({
                        conditionId:row.conditionId
                    })
                }).then(({data}) => {
                    if(data && data.code===0){
                        this.tableData = JSON.parse(data.data.conditionScript);
                        this.dataForm.conditionStatus = Number(data.data.conditionStatus);
                        this.dataForm.conditionDesc = data.data.conditionDesc;
                    }else{
                        this.$message.error(data.msg);
                    }
                });
            },
            //
            handleDelete(index, row) {
                this.tableData.splice(index, 1);
            },
            selectionChangeHandle(val) {
                //
                this.tableDataSelections = val;
            },
            moveDirection(direction) {
                if(this.tableDataSelections.length === 0) {
                    this.$confirm('', '', {
                        confirmButtonText: '',
                        showCancelButton: false,
                        type: 'warning',
                        center: true
                    });
                    return;
                };
                //
                var that = this,
                        index;
                that.tableDataSelections.sort((prev, next) => {
                    return that.tableData.indexOf(prev) - that.tableData.indexOf(next);
                });
                if (direction == "down") that.tableDataSelections.reverse();
                that.tableDataSelections.forEach(item => {
                    index = that.tableData.indexOf(item); //
                    if (direction == "up") that.moveUp(index);
                    else if (direction == "down") that.moveDown(index);
                });
            },
            moveUp(index) {
                //
                if (index == 0) return;
                this.tableData = swapItems(this.tableData, index, index - 1, "up");
            },
            moveDown(index) {
                //
                if (index == this.tableData.length - 1) return;
                this.tableData = swapItems(this.tableData, index, index + 1, "down");
            },
            describeClick(){
                var sortArr = [];
                if (this.tableData.length < 0) return;
                this.tableData.forEach((item, index) => {
                    sortArr.push(
                        item.expressionPrefix,
                        item.name,
                        item.operatorShow,
                        item.valueShow,
                        item.expressionSuffix,
                        item.connectorShow,
                    );
                });
                this.dataForm.conditionDesc = sortArr.toString().replace(/,/g," ")
            },
            saveClick(){
                var sortArr = [];
                if (this.tableData.length < 0) return;
                this.tableData.forEach((item, index) => {
                    sortArr.push({
                        expressionPrefix:item.expressionPrefix,
                        name:item.name,
                        operatorShow:item.operatorShow,
                        valueShow:item.valueShow,
                        expressionSuffix:item.expressionSuffix,
                        connectorShow:item.connectorShow,
                    });
                });
                if(this.isEdit ==true){
                    this.$http({
                        url: this.$http.adornUrl(this.API.savePersonMingceCondition),
                        method: 'post',
                        data: this.$http.adornData({
                            mingceId:this.checkedData.id,
                            conditionScript:JSON.stringify(sortArr),
                            conditionStatus:this.dataForm.conditionStatus,
                            conditionDesc:this.dataForm.conditionDesc
                        })
                    }).then((res) =>{
                        var resData = res.data;
                        if(resData.code == 0) {
                            this.$message({
                                message: '',
                                type: 'success'
                            });
                            this.$emit("addback",true);
                        } else {
                            this.$message.error(resData.msg);
                        }
                    })   
                }else if(this.isEdit == false){
                    this.$http({
                        url: this.$http.adornUrl(this.API.updatePersonMingceCondition),
                        method: 'post',
                        data: this.$http.adornData({
                            mingceId:this.checkedData.id,
                            conditionId:this.conditionId,
                            conditionScript:JSON.stringify(sortArr),
                            conditionStatus:this.dataForm.conditionStatus,
                            conditionDesc:this.dataForm.conditionDesc
                        })
                    }).then((res) =>{
                        var resData = res.data;
                        if(resData.code == 0) {
                            this.$message({
                                message: '',
                                type: 'success'
                            });
                            this.$emit("addback",true);
                        } else {
                            this.$message.error(resData.msg);
                        }
                    })   
                }
            },
        }
    }
</script>

<style lang="scss" scoped>
    .box{
        height: 630px;
        .el-aside {
            border-right: 1px solid #e6e6e6;
            padding-top: 10px; 
        }
        .el-table{
            height: 410px;
            overflow: auto;
        }
        .top-btn{
            float: right;
            padding: 0 0 10px 0;
        }
    }
    .el-main {
        padding: 0;
    }
</style>



