<template>
  <div id="myChart" :style="{width: '100%', height: '100%'}"></div>
</template>

<script>
  import $ from 'jquery'
  export default {
    name: 'Echarts',
    props:[],
    data () {
      return {
        myChart:null,
        chartFont:{
          titleFontSize:14,//
          legendFontSize:14,//
          axisLabelFontSize:12,// 
          tooltipFontSize:14,//tooltip
          symbolFontSize:8,//
          labelFontSize:12//label
        }
      }
    },
    mounted(){

    },
    watch:{

    },
    methods: {
      exportCharts(curCategoryaName){
        var img = new Image();
        img.src =this.myChart.getDataURL({
          type:"png",
          pixelRatio: 1,
          backgroundColor: '#fff'
        });
        img.onload=function(){
          var canvas=document.createElement("canvas");
          canvas.width=img.width;
          canvas.height=img.height;
          var ctx=canvas.getContext('2d');
          ctx.drawImage(img,0,0);
          var  dataURL=canvas.toDataURL('image/png');

          var a = document.createElement('a');
          // 
          var event = new MouseEvent('click');
          //
          a.download = curCategoryaName;
          // URLa.href
          a.href = dataURL;
          // a
          a.dispatchEvent(event);
        }
      },
      bar(data,opt) {
        this.$echarts.init(document.getElementById('myChart')).dispose();
        // domecharts
        this.myChart = this.$echarts.init(document.getElementById('myChart'))
        var datas = data.data;
        var xData = data.xData;//,
        var title = data.title?data.title:"";
        var color = (Array.isArray(data.color)&&data.color.length)?data.color: ["#6AB0B8"];
        var opt = opt?opt:{};

        var legendData = [];
        var newOpt;
        var series = [];

        for(var i = 0; i < datas.length; i++){
          series.push(
            {
              name:datas[i].seriesName,
              label:{
                show:true,
                position:"top",
                textStyle:{
                  color:"#666",
                  fontSize:this.chartFont.labelFontSize
                }
              },
              type: 'bar',
              barWidth: "25%",
              color:color[i]?color[i]:color[0],
              data: datas[i].data
            }
          )
          legendData.push(datas[i].seriesName);
        }
        var defaultOpt = {
          backgroundColor: '',
          tooltip:{},
          title: {
            text: title,
            left: '1%',
            top: '3%',
            textStyle: {
              color: '#666',
              fontSize:this.chartFont.titleFontSize
            }
          },
          legend: {
            show:true,
            type:"scroll",
            left:"30%",
            top: '6%',
            textStyle: {
              fontSize:this.chartFont.legendFontSize,
            },
            data:legendData
          },
          xAxis: {
            data: xData,
            type: 'category',
            axisLine: {
              lineStyle: {
                color: '#666',

              }
            },
            axisLabel: {
              color: '#666',
              fontSize: this.chartFont.axisLabelFontSize
            }
          },
          yAxis: {
            name: "",
            nameTextStyle: {
              color: '#666',
              fontSize: this.chartFont.axisLabelFontSize
            },
            axisLine: {
              lineStyle: {
                color: '#666'
              }
            },
            axisLabel: {
              color: '#666',
              fontSize: this.chartFont.axisLabelFontSize
            },
            splitLine: {
              show: false,
              lineStyle: {
                color: '#666'
              }
            },


          },
          grid:{
            left: '1%',
            right: '5%',
            top: '20%',
            bottom: '15%',
            containLabel: true
          },
          series: series
        };
        newOpt = $.extend(true, {}, defaultOpt, opt);
        this.myChart.setOption(newOpt);
        window.addEventListener("resize", function() {
          setTimeout(function() {
            try{
              this.myChart.resize();
            }catch(e){}
          }, 100)
        });
      },

      //
      //
      line(data,opt) {
        this.$echarts.init(document.getElementById('myChart')).dispose();
        // domecharts
        this.myChart = this.$echarts.init(document.getElementById('myChart'))
        var xData = data.xData;
        var datas = data.data;
        var title = data.title?data.title:"";
        var color = (Array.isArray(data.color)&&data.color.length)?data.color:['#b0e1cb'];
        var opt = opt?opt:{};

        var series = [];
        var legendData = [];
        var selected = {};
        var newOpt;//

        for(var i = 0; i < datas.length; i++){
          series.push({
            name: datas[i].seriesName,
            smooth: false,
            type: 'line',
            symbolSize: this.chartFont.symbolFontSize,
            symbol: 'circle',
            data: datas[i].data,
            color:color[i]?color[i]:color[0],
            areaStyle: {normal: {opacity:0.5}}
          });
          legendData.push(datas[i].seriesName);
        }

        var defaultOpt = {
          backgroundColor: "", //#404A59
          color:color,

          title: [{
            text: title,
            left: '1%',
            top: '6%',
            textStyle: {
              color: '#666',
              fontSize:this.chartFont.titleFontSize
            }
          }],
          tooltip: {
            trigger: 'axis',
            formatter: '{b} : {c}',
            textStyle:{
              fontSize:this.chartFont.tooltipFontSize
            }

          },
          legend: {
            show:false,
            type:"scroll",
            left:"30%",
            top: '6%',
            textStyle: {
              fontSize:this.chartFont.legendFontSize,
            },
            data:legendData,
            selected:selected
          },
          grid: {
            left: '1%',
            right: '5%',
            top: '16%',
            bottom: '6%',
            containLabel: true
          },
          toolbox: {
            "show": false,
            feature: {
              saveAsImage: {}
            }
          },
          xAxis: {
            type: 'category',
            "axisLine": {
              lineStyle: {
                color: '#666'
              }
            },
            "axisTick": {
              "show": true
            },
            axisLabel: {
              textStyle: {
                color: '#666',
                fontSize:this.chartFont.axisLabelFontSize
              }
            },
            boundaryGap: false,
            data: xData
          },
          yAxis: {
            "axisLine": {
              lineStyle: {
                color: '#666'
              }
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: '#666'
              }
            },
            "axisTick": {
              "show": false
            },
            axisLabel: {
              textStyle: {
                color: '#666',
                fontSize:this.chartFont.axisLabelFontSize
              }
            },
            type: 'value'
          },
          series:series
        }
        newOpt = Object.assign({},defaultOpt,opt)
        this.myChart.setOption(newOpt);
        window.addEventListener("resize", function() {
          setTimeout(function(){
            try{
              this.myChart.resize();
            }catch(e){}
          },100)
        });
      },

      //
      //xData
      pie(data,opt) {
        this.$echarts.init(document.getElementById('myChart')).dispose();
        // domecharts
        this.myChart = this.$echarts.init(document.getElementById('myChart'))
        var datas = data.data;
        var title = data.title?data.title:"";
        var color = (Array.isArray(data.color)&&data.color.length)?data.color:['#d74e67', '#0092ff', '#eba954', '#21b6b9','#60a900','#01949b',' #f17677'];
        var opt = opt?opt:{};

        var series = [];
        var selected = {};
        var legendData = [];
        var newOpt;

        //seriesdatas1
        for(var i = 0; i < datas.length; i++){
          series.push({
            name:datas[i].seriesName,
            type: 'pie',
            center:["center",'40%'],
            radius: ['0', '40%'],
            color:color,
            label: {
              formatter: '{b}\n{c}',
              fontSize:this.chartFont.labelFontSize
            },
            data: datas[i].data
          })

        }
        for(var i = 0; i < datas[0].data.length; i++){
          legendData.push(datas[0].data[i].name);
        }
        var defaultOpt = {
          backgroundColor:"",
          title: {
            text: title,
            left: 'center',
            top: '6%',
            textStyle: {
              color: '#666',
              fontSize:this.chartFont.titleFontSize
            }
          },

          tooltip: {
            trigger: 'item',
            formatter: "{b}: {c}",
            textStyle:{
              fontSize:this.chartFont.tooltipFontSize
            }

          },
          legend: {
            show:false,
            orient:'horizontal' ,
            type:"scroll",
            bottom:'10%',
            itemWidth: 14,
            itemHeight: 14,
            selected:selected,
            data:legendData,
            textStyle: {
              fontSize:this.chartFont.legendFontSize
            }
          },
          series:series
        };
        var newOpt = $.extend(true, {}, defaultOpt, opt)
        this.myChart.setOption(newOpt);
        window.addEventListener("resize", function() {
          setTimeout(function(){
            try{
              this.myChart.resize();
            }catch(e){}
          },100)
        });
      },
    }
  }
</script>

<style scoped>

</style>



