            initEchart(echartType){
              this.echartType = echartType

              let allDatas = this.allDatas.data
              var data = this.getChartData(this.categoryObj,allDatas)

              if(echartType == 'txt'){
                //
                this.showNmae = 'explanatoryNote'
                this.chartData = data
              }else if(echartType == 'writingSummary' ||echartType == 'functionalSymbol'){
               // 
                return false
              }else{
                //
                this.loading = true
                this.showNmae = 'echartsShow'
                this.$nextTick(function(){
                  this.renderChart(echartType,data)
                  this.loading = false
                })
              }

            },
            renderChart(chartType,chartData){
              console.log(chartData)
              this.chartData = chartData
              //
              var xAxis = {
                axisLabel: { //xAxisyAxisaxisaxisLabel
                  show: true, //truefalse
                  interval: 0, // 
                  rotate: -50 //
                  // formatter: function(value,index) { //
                  //     return "";
                  //
                  // }

                }
              }
              if(chartType == "line"){//
                var chartConfig = {
                  title:[{
                    x:"center",
                    left: "50%",
                  }],
                  legend:{
                    show:true
                  },
                  grid: {
                    right: '8%',
                    left:"3%"
                  },
                  toolbox: {
                    show: false,
                    feature: {
                      dataZoom: {
                        show:false,
                        yAxisIndex: 'none'
                      },
                      dataView: {readOnly: false},
                      magicType: {type: ['line', 'bar','pie']},
                      restore: {
                        show:false
                      },
                      saveAsImage: {}
                    }
                  },
                  yAxis: {
                    name:"",
                  },
                  tooltip:{
                    trigger:"item",
                    formatter:"{a} {b} : {c}"
                  }
                }
                if(chartData.xData.length > 4){
                  chartConfig.xAxis = xAxis;
                }
                this.$refs.echarts.line(chartData,chartConfig)
              }else if(chartType == "bar"){//
                var chartConfig = {
                  yAxis: {
                    name:""
                  },
                  tooltip:{
                    formatter:"{b} : {c}"
                  },
                  grid: {
                    left:"3%"
                  }
                }
                if(chartData.xData.length > 4){
                  chartConfig.xAxis = xAxis;
                }
                this.$refs.echarts.bar(chartData,chartConfig)
              }else if(chartType == "pie"){//


                this.$refs.echarts.pie(chartData,{
                  tooltip:{
                    formatter:"{b} : {c}{d}%"
                  },
                  series:[{
                    label: {
                      formatter: '{b}\n{c}{d}%',
                    },
                    radius: ['0', '60%'],
                    center:["center",'center'],
                  }]
                })


              }
            },
