              //
              var seriesNameArr = [];
              var allChartsDatas = allDatas.items;
              var xData = [];//
              var data = [];
              var categoryCode = categoryObj.categoryCode;
              var categoryName = categoryObj.categoryName;
              var categoryCodeArr = categoryCode.split(",");
              for(var k = 0; k < categoryCodeArr.length; k++){
                var categoryCodeItem = categoryCodeArr[k];
                var categoryData = [];
                var fomatCategoryData = [];

                for(var i = 0; i < allChartsDatas.length; i++){
                  if(allChartsDatas[i][categoryCodeItem]){
                    categoryData = allChartsDatas[i][categoryCodeItem];
                    break;
                  }
                }
                for(var i = 0; i < categoryData.length; i++){
                  if(k == 0){
                    xData.push(categoryData[i].cname);
                  }
                  var obj = {};
                  for(var key in categoryData[i]){
                    if(key == "count"){
                      obj[key] = categoryData[i]["value"]
                    } else if(key == "value") {
                      obj[key] = categoryData[i]["count"]
                    }
                    else{
                      obj[key] = categoryData[i][key];
                    }
                  }
                  obj.name = categoryData[i].cname;
                  fomatCategoryData.push(obj)

                }
                data.push({
                  seriesName:categoryName[k],
                  data:fomatCategoryData
                })
              }
              return {
                xData:xData,
                data:data,
                title:"",
                color:["#b0e1cb","#37d8ef","#e7bcf3"]
              }
            }
        }
    }
</script>

<style  lang="scss" scoped type="text/stylus">
  .el-header{
    height:32px!important;
  }
  .el-aside{
    border-right: 1px solid #c1c1c1;
  }
  .showIcon {
    height: 32px;
    ul{
     li{
       list-style-type: none;
       float: right;
       margin: 2px 2px;
       width: 30px;
       height: 30px;
        span{
          display:inline-block;
          cursor:pointer;
        }
        img,span{
          width: 30px;
          height: 30px;
        }
     }
    }
  }
  //  
  .font-desc {
    font-size: 1em;
    -webkit-user-select: auto;
    >>>p{
      text-indent: 2.4em;
      line-height: 25px;
      font-family: 'Times New Roman';
      margin:0;
      padding:0;
    }
    >>>.statis-num {
      font-family: 'Times New Roman';
      color: #498bed;
      font-weight: bold;
    }
  }
  //
  .el-main{
    position:relative;
  }
  .btn-list {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -150px;
    margin-left: -60px;
    li{
      margin-bottom: 30px;
      .btn{
        cursor: pointer;
      }
    }
  }
</style>



