<template>
  <div class="organization-tree">
    <el-tree ref="generalTree" 
    :accordion="accordion" 
    node-key="id" 
    :show-checkbox="checkbox" 
    :check-strictly="checkStrictly" 
    :default-checked-keys="defaultChecked" 
    :filter-node-method="filterNode" 
    :filterText="filterText" 
    :highlight-current="isHighLight"
     :expand-on-click-node="clickNode" 
     :default-expand-all="expandAll" 
     :default-expanded-keys="defaultExpandedKeys" 
     :data="treeData" 
     :props="defaultProps"
      @node-click="handleNodeClick" 
      @check="check" 
      @check-change="checkChange">
      <div class="custom-tree-node" slot-scope="{data}">
        <div @dblclick="nodeDbClick(data)">
          <span :class="data.id ==='88888888-8888-8888-8888-888888888888' || data.tag ==='system' ? 'xunijigou':'danwei'"></span> {{data[defaultProps.label]}}
        </div>
      </div>
    </el-tree>
  </div>
</template>

<script>
export default {
  name: "orgTree",
  props: {
    accordion: Boolean, // 
    treeData: Array,
    checkbox: Boolean,
    expandAll: Boolean, // 
    checkStrictly: Boolean,//
    defaultChecked: Array,//
    filterText: '',
    multiAgencyQuery: false,
    defaultProps: {
      type: Object,
      default: () => {
        return {
          children: 'children',
          label: 'name'
        }
      }
    },
    defaultExpandedKeys: {
      type: Array,
      default: () => {
        return ['88888888-8888-8888-8888-888888888888']
      }
    },
    clickNode: {
      type: Boolean,
      default: false
    },
  },
  created () {
    console.log(this.defaultProps)
  },
  data () {
    return {
      selectedNodeId: '',
      isHighLight: true,
      // expandAll: false,
    };
  },
  methods: {
    check: function (d1, d2) {
      this.$emit('check', d1, d2)
    },
    checkChange: function (dd, aa, cc) {
      this.$emit('check-change', dd)
      this.checkedKeysArr = []
      this.checkedKeysArr = this.$refs.generalTree.getCheckedKeys()
      this.$emit("orgTreeClick2", this.checkedKeysArr);
    },
    handleNodeClick (data, node) {
      console.log(node)
      this.$emit("node-click", data);
      this.checkedKeysArr = []
      this.checkedKeysArr.push(data.id)
      let Obj = {
        checkedKeysArr: this.checkedKeysArr,
        node: node
      }
      this.$emit("orgTreeClick2", this.checkedKeysArr);
      if (!this.multiAgencyQuery) {
        this.$emit("orgTreeHandle", data);
      }
    },
    // 
    getNode (v) {
      return this.$refs.generalTree.getNode(v)
    },
    // 
    getCheckedNodes () {
      return this.$refs.generalTree.getCheckedNodes()
    },
    getCheckedKeys () {
      return this.$refs.generalTree.getCheckedKeys()
    },
    getCurrentKey () {
      return this.$refs.generalTree.getCurrentKey()
    },
    // 
    setChecked (arr, isCheck, bol) {
      // 1.  key  data 2. boolean  3. boolean 
      return this.$refs.generalTree.setChecked(arr, isCheck, false)
    },
    setCheckedKeys (arr, bol) {
      // arr  key  bol  true 
      return this.$refs.generalTree.setCheckedKeys(arr, bol);
    },
    // 
    setCurrentKey (id) {
      if(!id) return
      return this.$nextTick(() => {
        this.$refs.generalTree.setCurrentKey(id);
      })
    },
    getHalfCheckedKeys () {
      return this.$refs.generalTree.getHalfCheckedKeys();
    },
    getCurrentNode () {
      return this.$refs.generalTree.getCurrentNode();
    },
    filterNode (value, data) {
      if (!value) return true;
      return data[this.defaultProps.label].indexOf(value) !== -1;
    },
    nodeDbClick (data) {
      this.$emit('orgTreeDbClick', data)
    },
    resetChecked () {
      //
      this.$nextTick(() => {
        this.$refs.generalTree.setCheckedKeys([]);
      });
    },
    firstNodeClick () {
      //
      this.$nextTick(() => {
        const firstNode = document.querySelector('.el-tree-node')
        firstNode.click();
      });
    },
    setCheckedNodes (arr) {
      this.$refs.generalTree.setCheckedNodes(arr);
    }
  },
  watch: {
    filterText (val) {
      this.$refs.generalTree.filter(val);
    },
    defaultChecked (val) {
      console.log(val)
    }
  }
};
</script>
<style lang="scss" scoped>
.organization-tree {
  height: 100%;
  .el-tree {
    min-width: 100%;
    font-size: 14px;
    overflow-y: auto;
    display: inline-block;
    .el-tree-node:focus > .el-tree-node__content {
      background-color: #88b1ca !important;
      color: #fff;
    }
  }
  .xunijigou,
  .danwei,
  .bumen {
    display: inline-block;
    width: 16px;
    height: 16px;
  }
  .xunijigou {
    /*background: url('../../assets/img/xunijigou.gif') no-repeat 0 3px;*/
    background: url('../../assets/img/xunijigou.png') no-repeat 0 0;
    padding-left: 5px;
    width: 20px;
    height: 16px;
    position: relative;
    top: 2px;
  }
  .danwei {
    /*background: url('../../assets/img/danwei.png') no-repeat 0 3px;*/
    background: url('../../assets/img/org_faren.png') no-repeat 0 0;
    padding-left: 5px;
    position: relative;
    top: 2px;
  }
  .bumen {
    background: url('../../assets/img/1_open.png') no-repeat 1px 0px;
    padding-left: 5px;
    position: relative;
    top: 2px;
  }
}
</style >



