<template>
    <div>
        <!--<el-button @click="previewPdf(scope.row)" type="text" size="small">-->
            <!--<div class="images" v-viewer="{ navbar: false}">-->
                <!--<img v-for="src in images" style="width: 100%" :src="src" :key="src" hidden>-->
                <!---->
            <!--</div>-->
        <!--</el-button>-->

        <el-dialog
                :visible="showDoc == true || showPdf == true  || showVideo == true || showImage == true"
                :show-close="true"
                :width="'80%'"
                :before-close="closePreviewClick"
                center
                :close-on-click-modal ="false">
            <div v-if="showDoc == true" class="dialog-body-content-base-style">
                <!--<iframe frameborder="0"-->
                        <!--:src="'https://view.officeapps.live.com/op/view.aspx?src=' +this.baseURL+ this.fileUrl"-->
                        <!--width='100%'>-->
                <!--</iframe>-->
                <span>Office</span>
            </div>
            <div v-else-if="showPdf == true" class="dialog-body-content-base-style"
                 style="justify-content: center; align-items: center">
                <embed :src="Url" style="width: 100%;height: 550px;"/>
            </div>
            <div v-else-if="showImage == true" class="dialog-body-content-base-style"
                 style="justify-content: center; align-items: center">
                <el-image :src="Url" fit="scale-down" style="width: 100%;height: 550px;"/>
            </div>
            <div v-else-if="showVideo == true"
                 class="dialog-body-content-base-style"
                 style="justify-content: center; align-items: center">
                <video-player class="video-player vjs-custom-skin"
                              ref="positiveVideoPlayer"
                              :playsinline="true"
                              :options="positivePlayerOptions"
                ></video-player>
            </div>
        </el-dialog>
    </div>
</template>
<script>
    export default {
        name: "orgFilePreview",
        //import
        props: {},
        components: {},
        data() {
            return {
                showDoc: false,
                showPdf: false,
                showVideo: false,
                showImage:false,
                baseURL:this.$api.url.baseUrl,
                Url: "",
                /**
                 *
                 */
                positivePlayerOptions: {
                    playbackRates: [0.5, 1.0, 1.5, 2.0], //
                    autoplay: true, //true,
                    muted: false, // 
                    loop: false, // 
                    preload: 'auto', // <video>auto,
                    language: 'zh-CN',
                    aspectRatio: '16:9', //  - "16:9""4:3"
                    fluid: true, // trueVideo.js player
                    sources: [{
                        type: "",
                        src: "" //url
                    }],
                    poster: "", //
                    // width: document.documentElement.clientWidth,
                    notSupportedMessage: '', //Video.js
                    controlBar: {
                        timeDivider: true,
                        durationDisplay: true,
                        remainingTimeDisplay: false,
                        fullscreenToggle: true  //
                    },
                    common: "positivePlayer",
                },

            };
        },

        methods: {
            /**
             * pdf
             */
            previewPdf(row) {
                let type = this.iconByType(row);
                this.Url = this.baseURL+row.fileUrl;

                if (type.indexOf("doc") != -1 || type.indexOf("docx") != -1 || type.indexOf("xsl") != -1 || type.indexOf("xslx") != -1) {
                    this.showDoc = true;
                } else if (type.indexOf("pdf") != -1) {
                    this.showPdf = true;
                } else if (type.indexOf("jpg") != -1 || type.indexOf("png") != -1 || type.indexOf("jpeg") != -1) {
                    this.showImage = true;
                } else if (type.indexOf("avi") != -1 || type.indexOf("mp4") != -1 || type.indexOf("webm") != -1 || type.indexOf("m4v") != -1 || type.indexOf("rmvb") != -1 || type.indexOf("mpg") != -1 || type.indexOf("3gp") != -1 || type.indexOf("swf") != -1 || type.indexOf("mkv") != -1) {
                    this.positivePlayerOptions.sources[0].src = this.Url;
                    this.positivePlayerOptions.sources[0].type = "video/mp4";
                    this.showVideo = true;
                } else {
                    this.$message("");
                }
            },
            /**
             * 
             */
            iconByType(row) {
                return row.fileName.substring(row.fileName.lastIndexOf(".") + 1, row.fileName.length)
            },
            closePreviewClick() {
                if (this.showDoc == true) {
                    this.showDoc = false
                } else if (this.showPdf == true) {
                    this.showPdf = false
                } else if (this.showVideo == true) {
                    this.showVideo = false
                }else if (this.showImage == true) {
                    this.showImage = false
                }

            },
        }
    }

</script>



