<template>
  <el-dialog
    title=""
    width="80%"
    :close-on-click-modal="false"
    :visible.sync="visible"
    :append-to-body="true">
    <el-container>
      <el-header>
        <el-form ref="form" :inline="true"  :model="form" label-width="100px" style="float: right;">
          <el-form-item>
            <el-button type="primary" size="small" @click="moveDirection('up')"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="small" @click="moveDirection('down')"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="small" @click="saveSort"></el-button>
          </el-form-item>
        </el-form>
      </el-header>
      <el-main>


        <el-table
          :data="tableData"
          size="small"
          style="width: 100%"
          highlight-current-row
          height="450px"
          @selection-change="selectChange">
          <el-table-column
            type="selection"
            width="55"
            :fixed="false">
          </el-table-column>
          <el-table-column
            v-for="item in tableCols"
            :fixed="item.fixed"
            :prop="item.colCode"
            :label="item.colName"
            :align="item.align"
            :width="item.width"
            :key="item.colCode"
            :show-overflow-tooltip="true">
          </el-table-column>
        </el-table>
      </el-main>
      <!--<el-footer>-->
        <!--<pagination ref="pagination" :page="page" @pageChange="pageChange" @pageChangeSize="pageChangeSize"></pagination>-->
      <!--</el-footer>-->
    </el-container>
  </el-dialog>
</template>

<script>
    import {swapItems} from '@/utils';
    import pagination from '@/components/baseComponents/newPagination'
    export default {
        name: "",
        components:{pagination},
        props:['allData'],
        data (){
          return {
            visible:false,
            form:{
              name:'',
              radio:''
            },
            tableDataSelections:[],
            page: {
              pageIndex:this.API.constObj.pageIndex,//
              pageSize:this.API.constObj.pageSize,//
              total: 0//
            },
            tableCols:[
              {prop:'name',label:'',width:'180',id:'1',fixed:false},
              {prop:'date',label:'',width:'180',id:'2',fixed:false},
              {prop:'address',label:'',width:'200',id:'3',fixed:false},
              {prop:'zip',label:'',width:'200',id:'4',fixed:false},
              {prop:'city',label:'',width:'300',id:'5',fixed:false},
              {prop:'province',label:'',width:'600',id:'6',fixed:false},
            ],
            tableData: [{
              date: '2016-05-03',
              name: 'dd',
              province: '',
              city: '',
              address: ' 1518 ',
              zip: 200333
            }, {
              date: '2016-05-02',
              name: '',
              province: '',
              city: '',
              address: ' 1518 ',
              zip: 200333
            }, {
              date: '2016-05-04',
              name: '',
              province: '',
              city: '',
              address: ' 1518 ',
              zip: 200333
            }, {
              date: '2016-05-01',
              name: '',
              province: '',
              city: '',
              address: ' 1518 ',
              zip: 200333
            }, {
              date: '2016-05-08',
              name: '',
              province: '',
              city: '',
              address: ' 1518 ',
              zip: 200333
            }]
          }
        },
        methods:{
          initDialog(){
            console.log(this.allData)
            this.tableData = this.allData.data
            this.tableCols = this.allData.cols

            console.log(this.tableData,this.tableCols)
            //
            this.visible = true

          },
          selectChange(val){
            console.log(val)
            this.tableDataSelections=val;
          },
          moveDirection(direction){ //
            var that=this,index;
            that.tableDataSelections.sort((prev,next)=>{
              return that.tableData.indexOf(prev)-that.tableData.indexOf(next)
            })
            if(direction=='down') that.tableDataSelections.reverse()
            that.tableDataSelections.forEach(item=>{
              index=that.tableData.indexOf(item); //
              if(direction=='up') that.moveUp(index);
              else if(direction=='down') that.moveDown(index)
            })
          },
          moveUp(index){//
            if(index==0) return;
            this.tableData=swapItems(this.tableData,index,index-1,'up');
          },
          moveDown(index){ //
            if(index==this.tableData.length-1) return;
            this.tableData=swapItems(this.tableData,index,index+1,'down');
          },
          saveSort(){//
            var sortArr=[];
            if(this.tableData.length<0) return;
            this.tableData.forEach((item,index)=>{
              sortArr.push({
                id:item.A0200,
                orderno:index+1
              })
            })
            this.$http({
              url: this.$http.adornUrl(this.API.sortJobInformation),
              method: 'post',
              data: this.$http.adornData({
                data:JSON.stringify(sortArr)
              })
            }).then(({data}) => {
              if(data && data.code===0){
                this.$message.success('');
                this.$emit('getJobInformation');
              }else{
                this.$message.error(data.msg);
              }
            });
          },
          pageChange(val){
            //
            this.page.pageIndex=val
          },
          pageChangeSize(val){
            //
            this.page.pageSize=val
          }
        }
    }
</script>

<style lang="scss" scoped type="text/stylus">
  >>>.el-header{
    height: inherit!important;
    padding: 0;
    .el-form-item:last-child{
      margin-right: 0;
    }
    .el-form-item{
      margin-bottom: 0;
    }
  }

  .el-footer{
    height: inherit!important;
  }

</style>



