<template>
  <el-select ref="elSelect" :value="valueTitle" :clearable="clearable" @clear="clearHandle" placeholder="" :disabled="disabled" STYLE="width: 100%;">
    <el-option :value="valueTitle" :label="valueTitle" class="options">
      <el-tree
        id="tree-option"
        ref="selectTree"
        :accordion="accordion"
        :data="options"
        :props="props"
        :node-key="props.value"
        :show-checkbox="multiple"
        :default-expanded-keys="defaultExpandedKey"
        :check-strictly="checkStrictly"
        @node-click="handleNodeClick"
        @check-change="handleCheckChange">
      </el-tree>
    </el-option>
  </el-select>
</template>

<script>
  export default {
    name: "el-tree-select",
    model: {
      prop: 'value',
      event: 'change'
    },
    props:{

      // 
      props:{
        type: Object,
        default: {
          value:'id',             // ID
          label: 'title',         // 
          children: 'children'    // 
        }
      },

      // ()
      options:{ type: Array},
      // 
      value:{ default: null },
      // 
      clearable:{ type:Boolean, default: false },
      disabled:{ type:Boolean, default: false },
      // 
      accordion:{ type:Boolean, default: false },
      //
      multiple:{ type:Boolean, default: false},
      //
      checkStrictly:{ type:Boolean, default: true},
    },
    data() {
      return {
        valueId: null,
        valueTitle:'',
        defaultExpandedKey:[]
      }
    },
    mounted(){
      this.valueId = this.value,    // 
      this.initHandle()
    },
    methods: {
      // 
      initHandle(){
        if(this.multiple){
          //
          if(this.valueId){
            let a = []
            a.push(this.valueId)
            this.valueId = a
            this.$refs.selectTree.setCheckedKeys(this.valueId);

            let options = this.valueId.map(item => {
              var node = this.$refs.selectTree.getNode(item); // node
              let tmpMap = {};
              tmpMap.value = node.key;
              tmpMap.label = node.label;
              return tmpMap;
            });
            let str = ''
            options.map((item,index) => {
              if(options.length-1 == index){
                str += item.label
              }else{
                str += item.label+' ';
              }
            });
            this.valueTitle =  str
            this.$emit('change',this.valueId)
          }else{
            this.valueTitle = ''
            this.valueId = null
            this.$refs.selectTree.setCurrentKey(this.valueId)
          }

        }else{
          //
          if(this.valueId){
            if(this.$refs.selectTree.getNode(this.valueId) !== null){
              console.log(this.$refs.selectTree.getNode(this.valueId))
              this.valueTitle = this.$refs.selectTree.getNode(this.valueId).data[this.props.label]     // 
              this.$refs.selectTree.setCurrentKey(this.valueId)       // 
              this.defaultExpandedKey = [this.valueId]      // 
            }
          }else{
            this.valueTitle = ''
            this.valueId = null
            this.$refs.selectTree.setCurrentKey(this.valueId)
          }
        }
        this.initScroll()
      },
      handleCheckChange() {
        var checkedKeys = this.$refs.selectTree.getCheckedKeys(); //  key 
        let options = checkedKeys.map(item => {
          var node = this.$refs.selectTree.getNode(item); // node
          let tmpMap = {};
          tmpMap.value = node.key;
          tmpMap.label = node.label;
          return tmpMap;
        });
        let str = ''
        options.map((item,index) => {
          if(options.length-1 == index){
            str += item.label
          }else{
            str += item.label+' ';
          }
        });
        this.valueTitle =  str
        // v-model
        this.$emit('change',checkedKeys)
      },
      // 
      initScroll(){
        this.$nextTick(()=>{
          let scrollWrap = document.querySelectorAll('.el-scrollbar .el-select-dropdown__wrap')[0]
          let scrollBar = document.querySelectorAll('.el-scrollbar .el-scrollbar__bar')
          scrollWrap.style.cssText = 'margin: 0px; max-height: none; overflow: hidden;'
          let dropdownList = document.querySelectorAll('.el-scrollbar .el-select-dropdown__list')[0]

          dropdownList.style.cssText = 'padding: 0px;'
          scrollBar.forEach(ele => ele.style.width = 0)
        })
      },

      // 
      handleNodeClick(node){
       if(!this.multiple){
         this.valueTitle = node[this.props.label]
         this.valueId = node[this.props.value]
         // v-model
         this.$emit('change',this.valueId)
         this.defaultExpandedKey = []
         //
         this.$refs.elSelect.blur()
       }
      },

      // 
      clearHandle(){
        this.valueTitle = ''
        this.valueId = null
        this.defaultExpandedKey = []
        this.clearSelected()

        if(!this.multiple){
          this.$emit('change','')
        }else{
          var checkedKeys = this.$refs.selectTree.getCheckedKeys(); //  key 
          for (let i = 0; i < checkedKeys.length; i++) {
            this.$refs.selectTree.setChecked(checkedKeys[i], false);
          }
        }
      },
      // 
      clearSelected(){
        let allNode = document.querySelectorAll('#tree-option .el-tree-node')
        allNode.forEach((element)=>element.classList.remove('is-current'))
      }

    },

    watch: {
      value(){
        if(this.value == null){
          this.value = ''
        }
        this.valueId = this.value
        if(!this.multiple){
          this.initHandle()
        }

      }
    },
  }
</script>

<style scoped>
  .el-scrollbar .el-scrollbar__view .el-select-dropdown__item{
    height: auto;
    max-height: 257px;
    padding: 0;
    overflow: hidden;
    overflow-y: auto;
  }
  .el-select-dropdown__item.selected{
    font-weight: normal;
  }
  ul li >>>.el-tree .el-tree-node__content{
    height:auto;
    padding: 0 20px;
  }
  .el-tree-node__label{
    font-weight: normal;
  }
  .el-tree >>>.is-current .el-tree-node__label{
    color: #409EFF;
    font-weight: 700;
  }
  .el-tree >>>.is-current .el-tree-node__children .el-tree-node__label{
    color:#606266;
    font-weight: normal;
  }
  .aaa >>> .el-select-dropdown__list {
    padding: 0!important;
  }
  .aaa{
    color: red;
  }
  .aaa >>> .el-scrollbar__view{
    padding: 0;
  }
</style>



