<template>
  <el-dialog
    title=""
    width="80%"
    :close-on-click-modal="false"
    :visible.sync="visible"
    :append-to-body="true">

    <el-container>
      <el-header>
        <el-form ref="form":inline="true"  :model="form" label-width="100px" style="float: right;">
          <el-form-item label="">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-radio v-model="form.radio" label="1"></el-radio>
            <el-radio v-model="form.radio" label="2"></el-radio>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="uploadAttachment"></el-button>
          </el-form-item>
        </el-form>
      </el-header>
      <el-main>
        <el-table
          class="main-tablebox"
          :data="tableData"
          size="small"
          style="width: 100%"
          max-height="450px">
          <el-table-column
            v-for="item in tableDataProp"
            :fixed="item.fixed"
            :prop="item.prop"
            :label="item.label"
            :width="item.width"
            :key="item.id">
          </el-table-column>
        </el-table>
      </el-main>
      <el-footer>
        <pagination ref="pagination" :page="page" @pageChange="pageChange" @pageChangeSize="pageChangeSize"></pagination>
      </el-footer>
    </el-container>
    <uploadAttachment ref="uploadAttachment"></uploadAttachment>
  </el-dialog>
</template>

<script>
    import pagination from '@/components/baseComponents/newPagination'
    import uploadAttachment from './uploadAttachment'
    export default {
        name: "copyHistory",
        components:{pagination,uploadAttachment},
        data() {
            return {
                visible:false,
                form:{
                    name:'',
                    radio:''
                },
                page: {
                    pageIndex:this.API.constObj.pageIndex,//
                    pageSize:this.API.constObj.pageSize,//
                    total: 0//
                },
                tableDataProp:[
                    {prop:'name',label:'',width:'180',id:'1',fixed:true},
                    {prop:'date',label:'',width:'180',id:'2',fixed:false},
                    {prop:'address',label:'',width:'200',id:'3',fixed:false},
                    {prop:'zip',label:'',width:'200',id:'4',fixed:false},
                    {prop:'city',label:'',width:'300',id:'5',fixed:false},
                    {prop:'province',label:'',width:'600',id:'6',fixed:false},
                ],
                tableData: [{
                    date: '2016-05-03',
                    name: '',
                    province: '',
                    city: '',
                    address: ' 1518 ',
                    zip: 200333
                }, {
                    date: '2016-05-02',
                    name: '',
                    province: '',
                    city: '',
                    address: ' 1518 ',
                    zip: 200333
                }, {
                    date: '2016-05-04',
                    name: '',
                    province: '',
                    city: '',
                    address: ' 1518 ',
                    zip: 200333
                }, {
                    date: '2016-05-01',
                    name: '',
                    province: '',
                    city: '',
                    address: ' 1518 ',
                    zip: 200333
                }, {
                    date: '2016-05-08',
                    name: '',
                    province: '',
                    city: '',
                    address: ' 1518 ',
                    zip: 200333
                }]
            }
        },
        methods: {
            initDialog(){
                //
                this.visible = true
            },
            uploadAttachment(){
                this.$refs.uploadAttachment.initDialog()
            },
            pageChange(val){
                //
                this.page.pageIndex=val
            },
            pageChangeSize(val){
                //
                this.page.pageSize=val
            }
        }
    }
</script>

<style lang="scss" scoped type="text/stylus">
  .el-header{
    height: inherit!important;
    padding: 0;
    .el-form-item:last-child{
      margin-right: 0;
    }
  }
  .el-form--inline .el-form-item{
    margin-bottom: 0;
  }
  .el-footer{
    height: inherit!important;
  }
</style>



