<template>
  <div>
    <el-container v-if="checkedData.componentsType == '0'">
      <el-header>
        <el-row>
          <el-col>
            <el-button type="primary" size="small" @click="saveClick"></el-button>
          </el-col>
        </el-row>
      </el-header>
      <el-main  v-loading="loading">
        <el-table
          height="515"
          id="applyTable"
          @header-dragend="colChange"
          border
          class="fixedtableHeight"
          ref="dragTable"
          :data="dataList"
          tooltip-effect="dark"
          style="width: 100%;margin-top:9px;border: 1px solid #EBEEF5;"
        >
          <el-table-column label="" type="index" width="50" align="center"></el-table-column>
          <el-table-column prop="listTitle" label="">
            <template slot-scope="scope">
              <el-input v-model="scope.row.listTitle" placeholder=""></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="listAlign" label="">
            <template slot-scope="scope">
              <el-select v-model="scope.row.listAlign" placeholder="" clearable style="width:100%">
                <el-option
                  v-for="item in align"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column prop="listVertical" label="">
            <template slot-scope="scope">
              <el-select v-model="scope.row.listVertical" placeholder="" clearable style="width:100%">
                <el-option
                  v-for="item in vertical"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column prop="listIsorder" label="">
            <template slot-scope="scope">
              <el-select v-model="scope.row.listIsorder" placeholder="" clearable style="width:100%">
                <el-option
                  v-for="item in sort"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column prop="listWidth" label="(px)">
            <template slot-scope="scope">
              <el-input v-model="scope.row.listWidth" placeholder=""></el-input>
            </template>
          </el-table-column>
        </el-table>
      </el-main>
    </el-container>

    <el-container v-if="checkedData.componentsType == '1'">
      <el-header>
        <el-row>
          <el-col>
            <el-button type="primary" size="small" @click="saveClick"></el-button>
          </el-col>
        </el-row>
      </el-header>
      <el-main  v-loading="loading"  v-show="showPhoto">
        <el-table
          height="515"
          id="applyTable"
          @header-dragend="colChange"
          border
          class="fixedtableHeight"
          ref="dragTable"
          :data="photoDataList"
          tooltip-effect="dark"
          style="width: 100%;margin-top:9px;border: 1px solid #EBEEF5;"
        >
          <el-table-column prop="photoWidth" label="(px)">
            <template slot-scope="scope" v-if="scope.row !== null">
              <el-input v-model="scope.row.photoWidth" placeholder=""></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="photoHeight" label="(px)">
            <template slot-scope="scope" v-if="scope.row !== null">
              <el-input v-model="scope.row.photoHeight" placeholder=""></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="photoShowrows" label="()">
            <template slot-scope="scope" v-if="scope.row !== null">
              <el-input-number v-model="scope.row.photoShowrows" controls-position="right"  :min="1" :max="100"></el-input-number>
            </template>
          </el-table-column>
          <el-table-column prop="photoWidthrow" label="(%)">
            <template slot-scope="scope" v-if="scope.row !== null">
              <el-input v-model="scope.row.photoWidthrow" placeholder=""></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="photoHeightrow" label="(px)">
            <template slot-scope="scope" v-if="scope.row !== null">
              <el-input v-model="scope.row.photoHeightrow" placeholder=""></el-input>
            </template>
          </el-table-column>

        </el-table>
      </el-main>
    </el-container>
  </div>
</template>
<script>
  import Sortable from 'sortablejs'
  export default{
    name:'columnAdvancedSetup',
    props:[
      'checkedData'
    ],
    data() {
      return {
        showPhoto:false,
        loading:true,
        tableColumnList: [],
        dataList: [],
        photoDataList:[],
        sort:[
          {
            value: '0',
            label: ''
          },
          {
            value: '1',
            label: ''
          },
        ],
        align:[
          {
            value: 'left',
            label: ''
          },
          {
            value: 'center',
            label: ''
          },
          {
            value: 'right',
            label: ''
          },
        ],
        vertical:[
          {
            value: 'top',
            label: ''
          },
          {
            value: 'center',
            label: ''
          },
          {
            value: 'bottom',
            label: ''
          },
        ],
      }
    },
    watch:{
      checkedData:{
        handler(){
          if(this.checkedData.componentsType == '0'){
            this.getTableData()
          }else  if(this.checkedData.componentsType == '1'){
            this.getPhotoData()
          }
        }
      }
    },
    created () {
      if(this.checkedData.componentsType == '0'){
        this.getTableData()
      }else  if(this.checkedData.componentsType == '1'){
        this.getPhotoData()
      }
    },
    methods:{
      getTableData(){
        //---
        this.$http({
          url: this.$http.adornUrl(this.API.getDataComponentsList),
          method: 'post',
          data: this.$http.adornData({
            componentsId:this.checkedData.componentsId,
            listFlog:'1'
          })
        }).then(({data}) =>{
          if(data.code === 0) {
            this.dataList = data.data;
            this.loading = false
          } else {
            this.$message.error(data.msg);
          }
        })
      },
      getPhotoData(){
        //---
        this.$http({
          url: this.$http.adornUrl(this.API.getDataComponentsPhoto),
          method: 'post',
          data: this.$http.adornData({
            componentsId:this.checkedData.componentsId,
            photoFlog:'1'
          })
        }).then(({data}) =>{
          if(data.code === 0) {
            if(data.data == {}){
              this.photoDataList = [{
                photoWidth:'100',
                photoHeight:'200',
                photoShowrows:'4',
                photoWidthrow:'50',
                photoHeightrow:'110'
              }]
            }else{
              let a = []
              a.push(data.data)
              this.photoDataList = a;
            }
            this.showPhoto = true
            this.loading = false
          } else {
            this.$message.error(data.msg);
          }
        })
      },
      saveClick(){
        //---
        if(this.checkedData.componentsType == '0'){
          //
          this.$http({
            url: this.$http.adornUrl(this.API.addDataComponentsList),
            method: 'post',
            data: this.$http.adornData({
              data:JSON.stringify(this.dataList),
              id:this.checkedData.componentsId,
              code:'1' //listFlog
            })
          }).then(({data}) =>{
            if(data.code === 0) {
              this.optionalColumnList = data.data;
              this.$message.success('');
              this.$emit('getDataList')
              // this.$emit('updateFn')
              this.$emit('closeDialog')
            } else {
              this.$message.error(data.msg);
            }
          })
        }else if(this.checkedData.componentsType == '1'){
          //
          this.$http({
            url: this.$http.adornUrl(this.API.saveDataComponentsPhoto),
            method: 'post',
            data: this.$http.adornData({
              photoWidth:this.photoDataList[0].photoWidth,
              photoHeight:this.photoDataList[0].photoHeight,
              photoShowrows:this.photoDataList[0].photoShowrows,
              photoWidthrow:this.photoDataList[0].photoWidthrow,
              photoHeightrow:this.photoDataList[0].photoHeightrow,
              componentsId:this.checkedData.componentsId,
              photoFlog:1
            })
          }).then(({data}) =>{
            if(data.code === 0) {
              this.$emit('updateFn')
              this.$emit('closeDialog')

              this.$message.success('');
            } else {
              this.$message.error(data.msg);
            }
          })



        }








      },

      /**
       * 
       */
      getTableColWidth () {
        var self = this
        var tableWidth = localStorage.getItem('applyTableColWidths')

        if (tableWidth) {
          for (var item = 0; item < self.tableColumnList.length; item++) {
            self.tableColumnList[item].width = JSON.parse(tableWidth)[item]

          }
        }
        console.log(JSON.parse(tableWidth)[item])
      },
      /**
       * 
       */
      colChange (newWidth, oldWidth, column, event) {
        setTimeout(() => {
          console.log(newWidth, oldWidth, column, event)
          console.log('')
          var applyTableColWidths = []
          var applyTable = document.getElementById('applyTable')
          var applyTableColgroup = applyTable.getElementsByTagName('colgroup')[0]
          var applyTableCol = applyTableColgroup.getElementsByTagName('col')
          for (var i = 0; i < applyTableCol.length; i++) {
            applyTableColWidths.push(applyTableCol[i].width)
          }
          console.log(JSON.stringify(applyTableColWidths))
          localStorage.setItem('applyTableColWidths', JSON.stringify(applyTableColWidths))
        }, 100)
      } ,

    }
  }
</script>
<style lang="scss" scoped>
  .el-header{
    height: 36px !important;
    padding: 0;
  }
  .el-header .el-button{
    float: right;
  }

  .el-main{padding: 0;}
  .hed-btn{
    float: right;
    padding: 0 0 10px 0;
  .el-dropdown{
    padding:0 10px 0 0;

  }
  }
  >>>.el-input-number--medium{
    width: 100%;
  }
</style>



