<template>
  <el-dialog
    title=""
    v-loading="loading"
    :visible.sync="visible"
    width="65%"
    center
    :before-close="modalClose"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
  >
    <div class="contentArea" :style="contentStyleObj">
      <el-col :span="6">
        <div class="input-suffix">
          <span class="searchName">:</span>
          <el-col :span="12">
            <el-input placeholder="" prefix-icon="el-icon-search" v-model="input2" size="small"></el-input>
          </el-col>
          <el-button type="primary" size="mini" icon="el-icon-search"></el-button>
        </div>
        <div class="treeContainer" :style="treeContentStyle">
          <general-tree ref="mergeTree" :treeData="treeData" @node-click="treeClick"></general-tree>
        </div>
      </el-col>
      <el-col :span="18">
        <div class="rightContainer">
          
          <div class="fromUp">  
             <el-divider content-position="left"></el-divider>
            <el-form
              class="merge-form"
              :label-position="labelPosition"
              label-width="100x"
              :model="formLabelAlign"
              :disabled="edit"
               size="small"
              >
              <el-col :span="12">
                <el-form-item label=":" prop>
                  <el-input v-model="formLabelAlign.name" type="text" placeholder=""></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label=":" prop>
                  <el-input v-model="formLabelAlign.region" type="text" placeholder=""></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label=":" prop>
                  <el-input v-model="formLabelAlign.type" type="text" placeholder=""></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label=":" prop>
                  <el-input v-model="formLabelAlign.type" type="text" placeholder=""></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label=":" prop>
                  <el-input v-model="formLabelAlign.type" type="text" placeholder=""></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label=":" prop>
                  <el-input v-model="formLabelAlign.type" type="text" placeholder=""></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label=":" prop>
                  <el-input v-model="formLabelAlign.type" type="text" placeholder=""></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label=":" prop>
                  <el-input v-model="formLabelAlign.type" type="text" placeholder=""></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label=":" prop>
                  <el-input v-model="formLabelAlign.type" type="text" placeholder=""></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label=":" prop>
                  <el-input v-model="formLabelAlign.type" type="text" placeholder=""></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label=":" prop>
                  <el-input v-model="formLabelAlign.type" type="text" placeholder=""></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label=":" prop>
                  <el-input v-model="formLabelAlign.type" type="text" placeholder=""></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label=":" prop>
                  <el-input v-model="formLabelAlign.type" type="text" placeholder=""></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label=":" prop>
                  <el-input v-model="formLabelAlign.type" type="text" placeholder=""></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label=":" prop>
                  <el-input v-model="formLabelAlign.type" type="text" placeholder=""></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label=":" prop>
                  <el-input v-model="formLabelAlign.type" type="text" placeholder=""></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="" prop>
                  <el-input v-model="formLabelAlign.type" type="text" placeholder=""></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="PAD" prop>
                  <el-input v-model="formLabelAlign.type" type="text" placeholder="PAD"></el-input>
                </el-form-item>
              </el-col>
            </el-form>
            <!-- <div class="merge-form">
              
            <org-from></org-from>
        
            </div> -->
             <el-button type="primary" size="mini"></el-button>
          </div>
          
          <div class="fromDown">
             <el-divider content-position="left"></el-divider>
             <el-col :span="10">
                <el-form>
                  <el-form-item label=":">
                    <tree-select
                      :data="treeData"
                      :defaultProps="defaultProps"
                      multiple
                      :nodeKey="nodeKey"
                      :checkedKeys="defaultCheckedKeys"
                      @popoverHide="popoverHide"
                    ></tree-select>
                  </el-form-item>
                </el-form>
           </el-col>
           <el-col :span="2">
             <el-button type="primary" size="mini"></el-button> 
          </el-col>
          </div>
        </div>
      </el-col>
    </div>
  </el-dialog>
</template>
<script>
import generalTree from "@/components/baseComponents/orgTree.vue";
import TreeSelect from "@/components/baseComponents/treeSelect.vue";
import orgFrom from "./orgForm.vue";
export default {
  name: "org-merge",
  components: {
    generalTree,
    TreeSelect,
    orgFrom
  },
  data() {
    return {
      loading: false,
      input2: "",
      contentStyleObj: {
        height: ""
      },
      treeContentStyle: {
        height: ""
      },
      treeData: [],
      labelPosition: "right",
      formLabelAlign: {
        name: "",
        region: "",
        type: ""
      },
      edit: true,
      defaultCheckedKeys: [],
      nodeKey: "id",
      defaultProps: {
        children: "children",
        label: "label"
      },
    };
  },
  props: {
    visible: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    //
    selectOrgTree() {
      let _this = this;
      _this.$axios.post(_this.$api.org.selectOrgTree).then(response => {
        if (response.data.code === 0) {
          let res = response.data;
          _this.treeData = res.data;
        } else {
          _this.$message.error(response.data.msg);
        }
      });
    },
    modalClose() {
      // 
      this.$emit("update:visible", false);
    },
    getHeight() {
      this.contentStyleObj.height = window.innerHeight - 300 + "px";
      this.treeContentStyle.height = window.innerHeight - 400 + "px";
    },
    treeClick() {},
    popoverHide(checkedIds, checkedData) {
    console.log(checkedIds);
    console.log(checkedData);
  },
  },
  created() {
    window.addEventListener("resize", this.getHeight);
    this.getHeight();
    this.selectOrgTree();
  },

  // - DOM
  mounted() {},

  beforeCreate() {}, // - 

  beforeMount() {}, // - 

  beforeUpdate() {}, // - 

  updated() {}, // - 

  beforeDestroy() {}, // - 

  destroyed() {
    window.removeEventListener("resize", this.getHeight);
  }, // - 

  activated() {} //keep-alive
};
</script>
<style  scoped>
.input-suffix {
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.searchName {
  line-height: 40px;
  height: 40px;
}
.treeContainer {
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #e6e6e6;
  min-height:50vh !important;
}
.rightContainer {
  margin-left: 5px;
}
.merge-form >>> .el-input {
 width:68%;
}
.merge-form >>> .el-input__inner {
  height: 32px;
  line-height: 32px;
}
.merge-form >>> .el-form-item {
  margin-bottom: 2px;
  text-align: right;
  padding-right: 10px;
}
.fromUp{
  text-align: right; 
}
.fromUp>>>.el-button{
  margin: 0 10px 0 0;
}
.fromUp .fromDown{
  clear: both;
}

</style>



