<template>
    <!--  -->
    <div class="sortBody">
        <div class="saveBtn">
            <el-button size="mini" type="primary" @click="goUp"></el-button>
            <el-button size="mini" type="primary" @click="goDown"></el-button>
            <el-button size="mini" type="primary" @click="saveSort"></el-button>
        </div>
        <div class="sortFrom">
            <el-table
                    :data="tableData"
                    ref="moviesTable"
                    :row-class-name="tableRowClassName"
                    style="width: 100%"
                    height="420"
                    highlight-current-row
                    :row-style="tableRowStyle"
                    :header-cell-style="tableHeaderColor"
                    @selection-change="handleSelectionChange"
                    @row-click="clickRow"
                     class="tableBox"
            >
                <el-table-column prop="orderIndex" label="" width="50"></el-table-column>
                <el-table-column prop="b0114" label="" width="150"></el-table-column>
                <el-table-column prop="b0101" :label="labelNames"></el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
    export default {
        name: "orgSort",
        //import
        components: {},
        props: {
            orgId: {
                type: String
            },
            labelNames: {
                type: String
            }
        },
        data() {
            return {
                tableData: [],
                selectdata: [],
                index: "",//
            };
        },

        // data
        computed: {},

        //data
        watch: {
            // obj:{
            //    handler:function(val,oldval){

            //    },
            //    deep:true,//
            // }
        },

        //
        methods: {
            tableRowStyle({row, rowIndex}) {
            },
            // table header
            tableHeaderColor({row, column, rowIndex, columnIndex}) {
                if (rowIndex === 0) {
                    return "background-color: #efefef;font-weight: 500;";
                }
            },
            //
            selectOrgSort() {
                let _this = this;
                let postData = {};
                postData.b0111 = _this.orgId;
                _this.$axios
                    .post(_this.$api.org.selectOrgById, postData)
                    .then(response => {
                        if (response.data.code === 0) {
                            let res = response.data;
                            _this.tableData = res.data;
                        } else {
                            _this.$message.error(response.data.msg);
                        }
                    })
                    .catch(function (error) {
                        _this.$message.error(error);
                    });
            },
            tableRowClassName({row, rowIndex}) {
                row.index = rowIndex;
            },
            handleSelectionChange(row) {

            },
            clickRow(row) { //
                let _this = this;
                _this.$refs.moviesTable.toggleRowSelection(row)
                _this.selectdata = row;
                this.index = row.index;
            },
            IsSort() {
                if (this.tableData.length == 0) {
                    this.$message("");
                    return;
                }
                if (this.tableData.length == 1) {
                    this.$message(",");
                    return;
                }
            },
            //
            goUp() {
                this.IsSort();
                if (this.selectdata == "") {
                    this.$confirm('', '', {
                        confirmButtonText: '',
                        showCancelButton: false,
                        type: 'warning',
                        center: true
                    });
                    return;
                }
                var that = this;
                if (this.index > 0) {
                    let upDate = that.tableData[this.index - 1];
                    that.tableData.splice(this.index - 1, 1);
                    that.tableData.splice(this.index, 0, upDate);
                    this.index = this.index - 1;
                } else {
                    this.$message('');
                }
            },
            //
            goDown() {
                this.IsSort();
                if (this.selectdata == "") {
                    this.$confirm('', '', {
                        confirmButtonText: '',
                        showCancelButton: false,
                        type: 'warning',
                        center: true
                    });
                    return;
                }
                var that = this;
                if ((this.index + 1) === that.tableData.length) {
                    this.$message('');
                } else {
                    let downDate = that.tableData[this.index + 1];
                    that.tableData.splice(this.index + 1, 1);
                    that.tableData.splice(this.index, 0, downDate);
                    this.index = this.index + 1;
                }
            },
            saveSort() {
                //
                let _this = this;
                let infoIdsArr = [];
                _this.tableData.forEach(item => {
                    infoIdsArr.push(item.b0111);
                });
                if (infoIdsArr.length <= 0) {
                    return;
                }
                let b01s = infoIdsArr.join(",").toString();
                let postData = {};
                postData.b0111s = b01s
                _this.$axios
                    .post(_this.$api.org.sortOrg, postData)
                    .then(response => {
                        if (response.data.code === 0) {
                            _this.$message.success("");
                        } else {
                            _this.$message.error("");
                        }
                    })
                    .catch(function (error) {
                        _this.$message.error(error);
                    });
            },
            resetTable() {
                _this.tableData = []
            }
        },

        // - this
        created() {
            //  this.selectOrgSort();
        },

        // - DOM
        mounted() {
        },

        beforeCreate() {
        }, // - 

        beforeMount() {
        }, // - 

        beforeUpdate() {
        }, // - 

        updated() {

        }, // - 

        beforeDestroy() {
        }, // - 

        destroyed() {

        }, // - 

        //keep-alive
        activated() {

        }
    };
</script>

<style scoped>
    .sortFrom{
        margin-top: 5px;
    }
    .sortFrom >>> .el-table td {
        border-bottom: none !important;
        padding: 6px 0;
    }

    .sortFrom >>> .el-table th {
        margin-top: 5px;
        padding: 6px 0;
    }
</style>


