<template>
  <el-dialog
    title=""
    :width="dialogWidth"
    :close-on-click-modal="false"
    :visible.sync="visible"
    :append-to-body="true">
    <el-container>
      <el-header>
        <el-row :gutter="20">
          <el-col :span="8">
            <el-select v-model="value" placeholder="" size="small" @change="personSelect">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            <el-button type="success" size="small" plain @click="prePerson" v-if="options.length !== 1"></el-button>
            <el-button type="success" size="small" plain @click="nextPerson" v-if="options.length !== 1"></el-button>
          </el-col>
          <el-col :span="16" style="text-align: right;">
            <el-button type="primary" size="small" plain @click="retireHandle"></el-button>
            <el-button type="primary" size="small" plain @click="entryDictionary"></el-button>
            <el-button type="primary" size="small" plain @click="printCivilForm"></el-button>
            <el-button type="primary" size="small" plain @click="cadresBrief"></el-button>
            <el-button type="primary" size="small" plain @click="appointApprove"></el-button>
            <el-button type="primary" size="small" plain @click="digitalArchives"></el-button>
            <div style="width:350px;display: inline-block" v-if="archivesShow">
              <el-select v-model="archiveValue" placeholder="" size="small" style="width: 140px">
                <el-option
                  v-for="item in archiveOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
              <el-radio v-model="radio" label="1"></el-radio>
              <el-radio v-model="radio" label="2"></el-radio>
            </div>
          </el-col>
        </el-row>
      </el-header>
      <el-main >
        <el-container style="height: 550px">
          <el-aside width="210px">
            <information-list ref="informationList" :secondArr="secondArr" :personId="value" @switchComponent="switchComponent" :key="timer"></information-list>
          </el-aside>
          <el-main>


            <div class="main-box">
              <div class="main-left" :style="{width:mainleftw,height:'100%'}">
                <component :ref="componentShowName" :is="componentShowName" :personId="value" :currMenu="currMenu"></component>
                <!--<information-grid :personId="value"></information-grid>-->
              </div>
              <div class="main-right" v-if="archivesShow">
                <div id="img-background">
                  <img style="height: 632px;"  src="@/assets/img/cover.jpg">
                  <div id="divperosnname">{{currentUser.label}}</div>
                </div>
              </div>
            </div>

          </el-main>
        </el-container>
      </el-main>
      <el-footer class="el-footer">
        <information-footer ref="informationFooter" :entryTree="entryTree"></information-footer>
      </el-footer>
    </el-container>


    <!---->
    <el-collapse-transition>
      <entry-dictionary ref="entryDictionary" v-if="entryDictionaryShow" :entryTree="entryTree" :personId="value" @dictionaryShow="dictionaryShow" @getPerWordConditions="getPerWordConditions"></entry-dictionary>
    </el-collapse-transition>

    <!---->
    <print-civil-form ref="printCivilForm" :personId="value"></print-civil-form>
    <!---->
    <cadres-brief-table ref="cadresBriefTable" :personId="value"></cadres-brief-table>
    <!---->
    <appoint-approve-table ref="appointApproveTable" :personId="value"></appoint-approve-table>

  </el-dialog>
</template>

<script>
    import informationList from './informationList'
    import informationGrid from './informationGrid'
    import informationFooter from './informationFooter'
    import printCivilForm from './informationTools/printCivilForm'
    import cadresBriefTable from './informationTools/cadresBriefTable'
    import appointApproveTable from './informationTools/appointApproveTable'
    import entryDictionary from './informationTools/entryDictionary'


    import testaa from './test'
    import test11 from './test1'
    export default {
        name: "leaderInfoIndex",
        components:{
          informationList,informationGrid,informationFooter,printCivilForm,cadresBriefTable,appointApproveTable,entryDictionary,testaa,test11
        },
        props:['info'],
        data() {
            return {
                visible:false,
                dialogWidth:'80%',
                mainleftw:'100%',
                radio:'1',
                options: [],
                archiveOptions:[{
                  value: '001',
                  label: '1'
                }, {
                  value: '002',
                  label: '2'
                }, {
                  value: '003',
                  label: '3'
                }],
                value: '',
                archiveValue:'',
                secondArr:[],
                archivesShow:false,
                entryDictionaryShow:false,
                entryTree:{
                  checkList:[],
                  entryTreeData:[],
                  wordNames:[]
                },
                componentShowName:'informationGrid',
                currMenu:{},
                rowIndex:'',
                timer: '',
                printObj: {
                  id: "printMe",
                  popTitle: 'good print',
                  extraCss: 'https://www.google.com,https://www.google.com',
                  extraHead: '<meta http-equiv="Content-Language"content="zh-cn"/>'
                },
                currentUser:{}
            }
        },
        created(){
          this.getinformation()
          this.getEntryTree()
        },
        methods: {
            initDialog(rowIndex){
                this.rowIndex = rowIndex
                //
                this.visible = true
                //
                let optionsArr = []
                this.info.forEach(function(item,index){
                  let obj ={
                    value:item.A0100,
                    label:item.A0101
                  }
                  optionsArr.push(obj)
                })
                this.options = optionsArr
                this.currentUser = this.options[this.rowIndex]
                //
                this.value = this.options[this.rowIndex].value
                //
                this.archivesShow = false
                this.dialogWidth = '80%'
                this.mainleftw='100%'
                this.getPerWordConditions()
                //
                this.timer = new Date().getTime()
            },
            getPersonData(){
              //
              //
              this.getPerWordConditions()
              //
            },
            personSelect(){
              //
              this.getPersonData()
              this.getCurper()
            },
            getCurper(){
              let This = this
              let curPer =  this.options.filter((item,index) =>{
                return item.value == This.value
              })
              this.currentUser = curPer[0]
            },
            prePerson(){
              //
              let This = this
              //index
              function objFn(obj, objIndex, objs){
                     return obj.value == This.value;
              }
              let currPersonIndex = this.options.findIndex(objFn)
              let prePersonIndex = currPersonIndex-1
              if(prePersonIndex == -1){
                this.$confirm('', '', {
                  confirmButtonText: '',
                  showCancelButton:false,
                  type: 'warning',
                  closeOnClickModal:false
                })
              }else{
                this.value = this.options[prePersonIndex].value
                this.personSelect()
              }
            },
            nextPerson(){
              //
              let This = this
              //index
              function objFn(obj, objIndex, objs){
                return obj.value == This.value;
              }
              let currPersonIndex = this.options.findIndex(objFn)
              let nextPersonIndex = currPersonIndex+1
              if(nextPersonIndex == this.options.length){
                this.$confirm('', '', {
                  confirmButtonText: '',
                  showCancelButton:false,
                  type: 'warning',
                  closeOnClickModal:false
                })
              }else{
                this.value = this.options[nextPersonIndex].value
                this.personSelect()
              }
            },
            retireHandle(){
                //
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning',
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: ''
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            entryDictionary(){
                //
                this.entryDictionaryShow = !this.entryDictionaryShow
            },
            getEntryTree(){
              //
              var params = {
                personId:this.value
              }
              this.$http({
                url: this.$http.adornUrl(this.API.getEntryTree),
                method: 'post',
                data: this.$http.adornData(params)
              }).then(({data}) => {
                console.log(data)
                if(data && data.code===0){
                  this.entryTree.entryTreeData = data.data
                }else{
                  this.$message.error(data.msg);
                }
              });
            },
            getPerWordConditions(){
              //
              let This = this
              var params = {
                personId:this.value
              }
              this.$http({
                url: this.$http.adornUrl(this.API.getPersonWordConditions),
                method: 'post',
                data: this.$http.adornData(params)
              }).then(({data}) => {
                console.log(data)
                if(data && data.code===0){
                  console.log(data)
                  let wordConditions = data.data
                  if(wordConditions[0] !== null){

                    this.entryTree.wordNames = wordConditions
                    wordConditions.forEach(function(item,index){
                      This.entryTree.checkList.push(item.wordId)
                    })
                  }else{
                    this.$set(this.entryTree,'wordNames',[])
                    console.log(this.entryTree)
                  }
                }else{
                  this.$message.error(data.msg);
                }
              });
            },
            dictionaryShow(){
                // 
                this.entryDictionaryShow = false
            },
            printCivilForm(){
                //
                this.$refs.printCivilForm.initDialog()
            },
            cadresBrief(){
                // 
                this.$refs.cadresBriefTable.initDialog()
            },
            appointApprove(){
                // 
                this.$refs.appointApproveTable.initDialog()
            },
            digitalArchives(){
                //
                this.archivesShow = true
                this.dialogWidth = '100%'
                this.mainleftw='auto'
            },
            getinformation(){
              //
              var params = {
                typeId:'a7d8da39365a4d2ab9e310cf58bc1f7d'
              }
              this.$http({
                url: this.$http.adornUrl(this.API.getDataTableTree),
                method: 'post',
                data: this.$http.adornData(params)
              }).then(({data}) => {
                if(data && data.code===0){
                  this.secondArr = data.data
                }else{
                  this.$message.error(data.msg);
                }
              });
            },
            switchComponent(val){
              console.log(val)
              this.currMenu = val
              //
              // if(val.name == ''){
              //   this.componentShowName = 'testaa'
              // }else if(val.name == ''){
              //   this.componentShowName = 'test11'
              // }else{
              //   this.componentShowName = 'informationGrid'
              // }
              this.componentShowName = 'informationGrid'
            }

        },

    }
</script>

<style lang="scss" scoped>
  /*element-ui */
  >>> .el-dialog .el-dialog__body{
    padding: 0;
  }
  .el-header{
    height: 44px!important;
    line-height: 44px;
    background: #F2F3F3;
    border-bottom: 1px solid #dedede;
  }
  .el-aside{
    background: #FBFBFB;
    border-right: 1px solid #dedede;
  }
  .el-main{
    padding: 0;
  }
  .el-footer{
    border-top: 1px solid #dedede;
    overflow: auto;
  }


  .main-box{
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    .main-left{
      /*width: 700px;*/
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      right: 500px;
      bottom: 0;
    }
    .main-right{
      width: 500px;
      background: #F0F6F9;
      overflow: hidden;
      float: right;
      #img-background{
        width: 100%;
        margin: 0px; padding: 0px;
        text-align: center;
        position: relative;
        #divperosnname {
          position: absolute;
          left: 206.17px;
          top: 372.88px;
          z-index: 999;
          font-family: ;
          font-size: 30px;
          color: black;
        }
      }
    }
  }
</style>



