<template>
  <div class="topbar">
    <div class="foot-left">
      <ul class="foot-left-ul clearfix">
        <li><span class="bg-img"></span><span class="border-r" id="unitName">{{searchConfigs.currentOrgItem.name}}</span></li>
        <li><span class="border-r" id="span_totalcount"><span id="spanTotalCount" style="display:inline-block; padding:0px 3px; text-align:center ">{{page.total}}</span></span></li>
      </ul>
    </div>
    <div  class="float-right" >
      <el-button type="primary" size="small"></el-button>
      <el-button type="primary" size="small"></el-button>
      <el-button type="primary" size="small"></el-button>
      <el-button type="primary" size="small" @click="addPersonnelHandle"></el-button>
      <el-button type="primary" size="small" @click="tabEvent('del')"></el-button>
      <el-button type="primary" size="small"></el-button>
      <el-button type="primary" size="small"></el-button>
      <el-button type="primary" size="small"></el-button>
    </div>
    <!-- -->
    <add-personnel v-if="addPersonnelVisible" ref='addPersonnel'></add-personnel>

  </div>
</template>

<script>
    import addPersonnel from '../add-personnel.vue'
    export default {
        name: "perInfoTableToolBar",
        components:{
            addPersonnel
        },
        props:['page','searchConfigs'],
        data() {
            return {
                addPersonnelVisible:false
            }
        },
        methods: {
            //
            addPersonnelHandle(){
                this.addPersonnelVisible=true;
                this.$nextTick(() => {
                    this.$refs.addPersonnel.init()
                })
            },
            tabEvent(val){
                //
              this.$emit('tabEvent',val)

            }
        }
    }
</script>

<style lang="scss" scoped>

</style>



