<template>
  <el-dialog
    title=""
    width="80%"
    :close-on-click-modal="false"
    :visible.sync="visible"
    :append-to-body="true">
    <el-container>
      <el-header>
        <div class="topbar clearfix">
          <div class="foot-left">
            <ul class="foot-left-ul clearfix">
              <li style="margin-left: 0;"><span class="bg-img"></span><span class="border-r" id="CurrentEditUnit"> {{perNum}} </span></li>
            </ul>
          </div>
        </div>

        <el-form ref="form" :inline="true"  :model="form" label-width="100px" style="float: right;">
          <el-form-item>
            <el-button type="primary" size="small" @click="moveDirection('up')"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="small" @click="moveDirection('down')"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="small" @click="saveSort"></el-button>
          </el-form-item>
        </el-form>
      </el-header>
      <el-main>
        <el-table
          class="main-tablebox"
          :data="tableInfo"
          size="small"
          style="width: 100%"
          height="450px"
          @selection-change="selectChange">
          <template v-for="(item,index) in tableCols">
            <el-table-column
              type="selection"
              width="55"
              v-if="index == 0">
            </el-table-column>
            <el-table-column
              width="50px"
              type="index"
              :index="indexMethod"
              label=""
              align="center"
              v-if="index == 0"
            ></el-table-column>

            <template  v-if="item != null">
              <el-table-column v-if="item.colCode == 'A0101'" :align="item.listAlign"  :prop="item.colCode" :width="item.listWidth" :label="item.colName" :show-overflow-tooltip="true">
                <template slot-scope="scope">
                  <span>{{scope.row.A0101}}</span>
                </template>
              </el-table-column>
              <el-table-column v-else  :align="item.listAlign" :prop="item.colCode" :width="item.listWidth" :label="item.colName" :show-overflow-tooltip="true"></el-table-column>
            </template>
          </template>
        </el-table>
      </el-main>
    </el-container>
  </el-dialog>
</template>

<script>
  import {swapItems} from '@/utils';
  import pagination from '@/components/baseComponents/newPagination'
  export default {
    name: "peopleSort",
    components:{pagination},
    props:['tableData'],
    data (){
      return {
        visible:false,
        form:{
          name:'',
          radio:''
        },
        tableDataelections:[],
        perNum:25,
        tableInfo:{},
        tableCols:{}
      }
    },
    methods:{
      initDialog(){
        //
        this.visible = true
        this.tableInfo = this.tableData.info.concat()
        this.tableCols = this.tableData.cols.concat()
      },
      indexMethod(index) {
        //
        return index + 1;
      },
      selectChange(val){
        console.log(val)
        this.tableDataelections=val;
      },
      moveDirection(direction){ //
        var that=this,index;
        that.tableDataelections.sort((prev,next)=>{
          return that.tableInfo.indexOf(prev)-that.tableInfo.indexOf(next)
        })
        if(direction=='down') that.tableDataelections.reverse()
        that.tableDataelections.forEach(item=>{
          index=that.tableInfo.indexOf(item); //
          if(direction=='up') that.moveUp(index);
          else if(direction=='down') that.moveDown(index)
        })
      },
      moveUp(index){//
        if(index==0) return;
        this.tableInfo=swapItems(this.tableInfo,index,index-1,'up');
      },
      moveDown(index){ //
        if(index==this.tableInfo.length-1) return;
        this.tableInfo=swapItems(this.tableInfo,index,index+1,'down');
      },
      saveSort(){//
        var sortArr=[];
        if(this.tableInfo.length<0) return;
        this.tableInfo.forEach((item,index)=>{
          sortArr.push({
            orgId:item.orgId,
            orderno:index+1
          })
        })
        this.$http({
          url: this.$http.adornUrl(this.API.orgSort),
          method: 'post',
          data: this.$http.adornData({
            data:JSON.stringify(sortArr)
          })
        }).then(({data}) => {
          if(data && data.code===0){
            this.$message.success('');
            this.getUserData();
          }else{
            this.$message.error(data.msg);
          }
        });
      },
    }
  }
</script>

<style lang="scss" scoped type="text/stylus">
  .topbar{
    display:inline-block;
    .foot-left{
      height:36px;
      line-height:36px;
    }
  }
  >>>.el-header{
    height: 36px!important;
    padding: 0;
    .el-form-item:last-child{
      margin-right: 0;
    }
    .el-form-item{
      margin-bottom: 0;
    }
  }

  .el-footer{
    height: inherit!important;
  }

</style>



