<template>
  <div class="managementArchTreeSelect">
    <el-cascader v-model="selectedOptions" :options="treeList" placeholder="" :change-on-select="true" :clearable="true" :show-all-levels="false" :props=treeProps expand-trigger="hover" @change="handleChange" ref="refHandle">
    </el-cascader>
  </div>
</template>
<script>
import bus from '../../../common/bus';
export default {
  name: 'AreaTreeSelect', //
  props: [
    'defaultOptions'//
  ],
  data () {
    return {
      selectedOptions: [],//--eg:['code']  ['code','code']
      treeProps: {
        value: 'areaId',
        label: 'areaName',
        children: 'children'
      },
      oldTreeList: [],//
      treeList: []//
    };
  },
  computed: {

  },
  created () {
    this.getDataList();
  },
  watch: {
    selectedOptions () {
      if (this.$refs.refHandle) {
        this.$refs.refHandle.dropDownVisible = false; //
      }
    }
  },
  methods: {
    /**
     * change
     * OrgTreeSelectCallvalue
     * @param {Array} valArray 
     */
    handleChange (valArray) {
      let findId = '0';
      let returnObj = {};
      if (valArray && valArray.length > 0) {
        findId = valArray[valArray.length - 1];
        returnObj = this.findObjById(findId)
      } else {
        returnObj.areaId = '0';
        returnObj.areaLevel = '0';
      }
      this.$emit('TreeSelectCall', returnObj);
    },
    /**
     * id
     * @param {Number} id id
     * @return {Object} 
     */
    findObjById (areaId) {
      let findObject = {};
      for (let i = 0; i < this.oldTreeList.length; i++) {
        if (areaId === this.oldTreeList[i].areaId) {
          findObject = this.oldTreeList[i];
          break;
        }

      }
      return findObject;
    },
    /**
     * 
     */
    getDataList () {
      this.$http({
        url: this.$http.adornUrl(this.API.getAllAreaInfoArea),
        method: 'post',
        data: this.$http.adornData({

        })
      }).then((res) => {
        var resData = res.data;
        if (resData.code === 0) {
          this.oldTreeList = resData.data;
          this.treeList = this.commonFun.toTreeDataNormal(resData.data, { childKey: 'areaId', fatherKey: 'areaPid' });
          let returnObj = {};
          if (this.defaultOptions) {
            let mappingObj = {
              childKey: 'areaId',
              fatherKey: 'areaPid'
            };
            let newArray = this.commonFun.findTreeNodeById(this.oldTreeList, this.defaultOptions, mappingObj);
            this.selectedOptions = this.commonFun.createArrIdAndReverse(newArray, "areaId");
            returnObj = this.findObjById(this.defaultOptions);
          } else if (this.defaultOptions === "") {
            returnObj = {};
          } else {
            this.selectedOptions.push(this.treeList[0].areaId);
            returnObj = this.treeList[0];
          }
          this.$emit('TreeSelectCall', returnObj);
        } else {
          this.$message.error(resData.msg);
        }
      })
    }
  },
  mounted () {

  }
}
</script>
<style lang="scss" scoped>
.el-scrollbar .el-scrollbar__view .el-select-dropdown__item {
  height: auto;
  padding: 0;
}

.el-select-dropdown__item.selected {
  font-weight: normal;
}

ul li >>> .el-tree .el-tree-node__content {
  height: auto;
}

.el-tree-node__label {
  font-weight: normal;
}

.el-tree >>> .is-current .el-tree-node__label {
  color: #409eff;
  font-weight: 700;
}

.el-tree >>> .is-current .el-tree-node__children .el-tree-node__label {
  color: #606266;
  font-weight: normal;
}
</style>



