<template>
  <el-dialog
    title=""
    :visible.sync="visible"
    :append-to-body="true">
    <el-form>
      <el-form-item label="">
        <el-switch v-model="tabSettings" active-color="#13ce66" inactive-color="#bbb"> </el-switch>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>

<script>
  export default {
    data(){
      return{
        visible: false,
      }
    },
    methods:{
      init () {
        this.visible = true
      }
    },
    computed: {
      tabSettings: {
        get () { return this.$store.state.common.tabSettings },
        set (val) { this.$store.commit('common/updateTabSettings', val) }
      }
    }
  }
</script>

<style>
</style>



