<template>
  <el-dialog
    class="add-personnel"
    title=""
    width="560px"
    :close-on-click-modal="false"
    :visible.sync="visible"
    :append-to-body="true">

    <el-row style="margin: 27px 0;">
      <el-col :span="11">
        <el-upload
          ref="upload"
          class="avatar-uploader"
          :action="action"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :on-change="change"
          :before-upload="beforeAvatarUpload"
          :auto-upload="false">
          <img v-if="imageUrl" :src="imageUrl" class="avatar">
          <img v-else :src="noPic" class="avatar">
        </el-upload>
      </el-col>
      <el-col :span="13">
        <div class="img-text">
          5:4JPGJPEG150K#3492C4 R52G146B196
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col style="text-align: center">
        <el-button type="primary" @click="clearPic"></el-button>
        <el-button type="success" @click="savePic"></el-button>
      </el-col>
    </el-row>

  </el-dialog>
</template>

<script>
  export default {
    name: "selectPicture",
    props:['personId','personId'],
    data() {
      return {
        visible:false,
        imageUrl: '',
        noPic:require("@/assets/img/no_pic.jpg"),
        action:'',
        file:null
      }
    },
    created(){
      this.action = this.$host + this.API.uploadPersonPhoto
    },
    methods: {
      initDialog(){
        //
        this.visible = true
        //
        this.imageUrl = ''
        this.file = null
      },
      change(file){
        //
        console.log(file)
        this.file = file
        this.imageUrl = URL.createObjectURL(file.raw);
        this.$emit('getFile',this.file)
        console.log(this.imageUrl)
      },
      clearPic(){
        //
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.imageUrl = ''
          this.file = null
          this.$emit('getFile','')
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      savePic(){
        console.log(this.file)
        if(this.file == null){
          this.$message({
            message: '',
            type: 'warning'
          });
        }else{
          let formData = new FormData()
          formData.append("file", this.file.raw);
          formData.append("a0100", this.personId);

          this.$http({
            headers: {
              'Content-Type': 'multipart/form-data;'
            },
            url: this.$http.adornUrl(this.API.uploadPersonPhoto),
            method: 'post',
            data: formData
          }).then(({data}) => {
            if(data && data.code===0){
              this.visible = false
              this.$message({
                message: '',
                type: 'success'
              });
            }else{
              this.$message.error(data.msg);
            }
          });
        }
      },
      handleAvatarSuccess(res, file) {
        // this.imageUrl = URL.createObjectURL(file.raw);
        console.log( this.imageUrl)
      },
      beforeAvatarUpload(file) {
         console.log(file)
        // this.imageUrl = file.url;


      },
    }
  }
</script>

<style lang="scss"  scoped>
  /deep/.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    margin-left: 10px;
  }
  /deep/ .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  /deep/.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 240px;
    height: 300px;
    display: block;
  }
  .img-text{
    height: 268px;line-height: 26px;padding: 0 20px;text-align: justify;
  }


</style>



