<template>
  <el-dialog
    title=""
    width="500px"
    top="30px"
    :close-on-click-modal="false"
    :visible.sync="visible">
    <el-container>
      <el-header>
        <el-row>
          <el-col>
            <el-button style="float: right;" type="primary" @click="save"></el-button>
          </el-col>
        </el-row>
      </el-header>
      <el-main>
        <org-tree ref="tree" :defaultProps="defaultProps" :treeData="nameListData"  @node-click="nameListTreeClick"></org-tree>
      </el-main>
    </el-container>
  </el-dialog>
</template>

<script>
  import orgTree from "@/components/baseComponents/orgTree.vue"
  export default {
    name: "addList",
    components:{
      orgTree
    },
    data () {
      return {
        visible:false,
        nameListData:[
          {"id":"1","listName":"","children":[]},
          {"id":"2","listName":"","children":[]}
        ],
        nameListSelect:'',
        defaultProps: {
          children: 'children',
          label: 'listName'
        },
        ids:[]
      }
    },
    created(){
      this.getNameList();
    },
    methods:{
      initDialog(ids){
        this.visible = true
        this.ids = ids
      },
      getNameList(){
        //
        this.$http({
          url: this.$http.adornUrl(this.API.getNameList),
          method: 'post',
          data: this.$http.adornData()
        }).then(({data}) => {
          if(data && data.code===0){
            this.nameListData[0].children=data.data['1'];
            this.nameListData[1].children=data.data['2']
          }else{
            this.$message.error(data.msg);
            this.nameListData=[];
          }
        });
      },
      nameListTreeClick(data){
        this.nameListSelect=data;
        console.log(data)
      },
      save(){
        //
        let param = {
          listType:this.nameListSelect.listType,
          listId:this.nameListSelect.listId,
          a0100:this.ids
        }
        this.$http({
          url: this.$http.adornUrl(this.API.saveNameListPerson),
          method: 'post',
          data: this.$http.adornData(param)
        }).then(({data}) => {
          if(data && data.code===0){
            this.$message({
              type: 'success',
              message: '!'
            });
          }else{
            this.$message.error(data.msg);
          }
        });

      }
    }
  }
</script>

<style  lang="scss" scoped type="text/stylus">
  .el-container{
    height:338px;
  }
  .el-main{
    padding-top:0;
  }
  .el-header{
    height: 36px!important;
    line-height: 36px;
  }
  .el-checkbox-group .el-checkbox:first-child
  {
    margin-left:20px;
  }
  .el-form-item__content .el-checkbox-group{
    line-height:32px;
  }
  >>> .el-input--medium .el-input__inner{
    border:none;
  }
  .commonsty-form .el-form-item{
    position:relative;
    min-height:38px;
  }
  .commonsty-form .el-form-item /deep/ .el-form-item__label{
    display:inline-block;
    height : 100%;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    padding-left: 6px;
    border-right: 1px solid #d1cece;
    background: #f9f9f9;
  }
</style>



