<template>
  <el-dialog
    class="checkoutScheme"
    title=""
    width="50%"
    top="30px"
    :close-on-click-modal="false"
    :visible.sync="visible">
    <el-tabs type="border-card">

      <el-tab-pane label="" class="zbjy">
       <el-row>
         <el-col :span="24">
           <el-form :inline="true" :model="formData" class="demo-form-inline" size="small">
             <el-form-item label="">
               <el-select v-model="formData.checkoutName" placeholder="">
                 <el-option label="" value="rmbjy"></el-option>
                 <el-option label="" value="xxjjy"></el-option>
               </el-select>
             </el-form-item>
             <el-form-item >
               <el-input v-model="selectTreeData.name" placeholder="" size="small" @focus="selectOrg"></el-input>
             </el-form-item>
             <el-form-item >
               <el-button type="primary" size="small"></el-button>
             </el-form-item>
           </el-form>
         </el-col>
       </el-row>
        <el-row>
          <el-col :span="24">
            <el-table
              ref="multipleTable"
              :data="tableData"
              height="440"
              style="width: 100%">
              <el-table-column
                type="index"
                width="55">
              </el-table-column>
              <el-table-column
                type="selection"
                width="55">
              </el-table-column>
              <el-table-column
                label=""
                width="145">
                <template slot-scope="scope">{{ scope.row.date }}</template>
              </el-table-column>
              <el-table-column
                prop="name"
                label="">
              </el-table-column>
            </el-table>
          </el-col>
        </el-row>
      </el-tab-pane>


      <el-tab-pane label="">
          <el-row>
            <el-col :span="24">
              <el-button type="primary" style="float: right;margin-bottom: 15px;" size="small"></el-button>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24">
              <el-table
                ref="multipleTable"
                :data="tableData"
                tooltip-effect="dark"
                height="440"
                style="width: 100%">
                <el-table-column
                  type="index"
                  width="55">
                </el-table-column>
                <el-table-column
                  label=""
                  prop="date"
                  width="155">
                  <template slot-scope="scope">{{ scope.row.date }}</template>
                </el-table-column>
                <el-table-column
                  prop="name"
                  label=""
                  width="255">
                </el-table-column>
                <el-table-column
                  prop="name"
                  label="">
                </el-table-column>
              </el-table>
            </el-col>
          </el-row>
      </el-tab-pane>

      <el-tab-pane label="">
        <el-row>
          <el-col :span="24">
            <el-button type="primary" style="float: right;margin-bottom: 15px;" size="small"></el-button>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-table
              ref="multipleTable"
              :data="tableData"
              tooltip-effect="dark"
              height="440">
              <el-table-column
                type="index"
                width="55">
              </el-table-column>
              <el-table-column
                prop="date"
                label=""
                width="155">
                <template slot-scope="scope">{{ scope.row.date }}</template>
              </el-table-column>
              <el-table-column
                prop="name"
                label="">
              </el-table-column>
              <el-table-column
                prop="name"
                label=""
                width="155">
              </el-table-column>
              <el-table-column
                prop="name"
                label=""
                width="255">
              </el-table-column>
            </el-table>
          </el-col>
        </el-row>
      </el-tab-pane>
    </el-tabs>

    <org-tree-select ref="orgTreeSelect" @getSelectTreeData="getSelectTreeData"></org-tree-select>
  </el-dialog>
</template>

<script>
  import orgTreeSelect from '@/components/baseComponents/orgTreeSelect'
  export default {
    name: "checkoutScheme",
    components:{
      orgTreeSelect
    },
    data () {
      return {
        visible:false,
        formData:{
          checkoutName:'',
          organization:''
        },
        tableData: [{
          date: '2016-05-03',
          name: '',
          address: ' 1518 '
        }, {
          date: '2016-05-02',
          name: '',
          address: ' 1518 '
        }, {
          date: '2016-05-04',
          name: '',
          address: ' 1518 '
        }, {
          date: '2016-05-01',
          name: '',
          address: ' 1518 '
        }, {
          date: '2016-05-08',
          name: '',
          address: ' 1518 '
        }, {
          date: '2016-05-06',
          name: '',
          address: ' 1518 '
        }, {
          date: '2016-05-07',
          name: '',
          address: ' 1518 '
        }],
        multipleSelection: [],
        selectTreeData:{}
      }
    },
    methods:{
      initDialog(){
        this.visible = true
        this.selectTreeData.name = ''
      },
      selectOrg(){
        this.$refs.orgTreeSelect.initDialog()
      },
      getSelectTreeData(data){
        this.selectTreeData = data
      },
      save(){
        //
        console.log(this.formData)
      }
    }
  }
</script>

<style  lang="scss" scoped type="text/stylus">
  >>>.zbjy .el-form--inline .el-form-item{
   margin-bottom: 14px;
  }
</style>



