<template>

  <el-dialog
    class="checkoutScheme"
    title=""
    width="600px"
    top="30px"
    :close-on-click-modal="false"
    :append-to-body="true"
    :visible.sync="visible">
      <el-container>
        <el-header>
          <el-input
            placeholder=""
            v-model="filterText"
            size="small"
            style="width: 200px;">
          </el-input>
          <el-button type="primary" size="small" style="margin-left: 10px;" @click="resetText"></el-button>


        </el-header>
        <el-main>
          <org-tree ref="tree" :treeData="treeData"
                    :checkbox='multiAgencyQuery'
                    :filterText='filterText'
                    :checkStrictly='!includeSubordinate'
                    @node-click="orgTreeClick" @orgTreeDbClick="orgTreeDbClick"></org-tree>
        </el-main>
      </el-container>
  </el-dialog>

</template>

<script>
  import orgTree from '@/components/baseComponents/orgTree'
  export default {
    name:'orgTreeSelect',
    components:{
      orgTree
    },
    data() {
      return {
        visible:false,
        filterText:'',
        includeSubordinate:false,
        multiAgencyQuery:false,
        treeData:[],
      };
    },
    created(){
      this.getDataTree();
    },
    methods: {
      initDialog(){
        this.visible = true
      },
      filterNode(value, data) {
        if (!value) return true;
        return data.label.indexOf(value) !== -1;
      },
      getDataTree(){//
        this.$http({
          url: this.$http.adornUrl(this.API.getOrganizationTree),
          method: 'post',
          data: this.$http.adornData()
        }).then(({data}) => {
          if(data && data.code===0){
            this.treeData=data.data;
          }else{
            this.$message.error(data.msg);
          }
        });
      },
      //
      orgTreeClick(data) {
        console.log(data)
        this.$emit('getSelectTreeData',data)
      },
      orgTreeDbClick(data){
        this.visible = false
        this.$emit('getSelectTreeData',data)
      },
      resetText(){
        this.filterText = ''
      }
    },


  };
</script>
<style lang="scss" scoped>
  >>>.el-form--inline{
    text-align: center;
  }
  .el-container >>> .el-header{
    height: 50px!important;
    line-height: 50px;
    text-align: center;
    background: #daedf4;
  }
  >>>.el-dialog .el-dialog__body{
    padding: 0;
    border-radius: 0;
  }
  /*el-tree */
  .organization-tree >>> .el-tree{
    width: 100%;
    height: 480px;
  }
  >>> .el-tree>.el-tree-node{
    display: inline-block !important;
    min-width: 100%;
  }

</style >



