<template>
    <el-form
            :inline="isInline"
            :model="value"
            label-position="right"
            :label-width="formConfig.labelWidth"
            :rules="rules"
            size='small'
    >
        <slot name="formItem"/>
        <el-form-item
                v-for="(item,index) in formConfig.formItemList"
                :key="index"
                :label="item.label"
                :prop="item.prop"
                v-show="item.type!='hidden'"
        >
            <el-input
                    v-if="item.type=='input'"
                    v-model="value[item.prop]"
                    :disabled="item.disabled"
                    :clearable="true"
                    :placeholder="item.placeholder"
            ></el-input>
            <el-select
                    :clearable="true"
                    v-else-if="item.type=='select'"
                    v-model="value[item.prop]"
                    :disabled="item.disabled"
                    :placeholder="item.placeholder"
            >
                <el-option
                        v-for="(optItem,index) in item.optList"
                        :key="index"
                        :label="optItem.label"
                        :value="optItem.value"
                ></el-option>
            </el-select>
            <el-input type="hidden"
                      v-else-if="item.type=='hidden'"
                      v-model="value[item.prop]"
                      :value="item.defaultValue"
            ></el-input>
            <el-date-picker
                    :value-format="item.dateFormate"
                    v-else
                    v-model="value[item.prop]"
                    :type="item.type"
                    :disabled="item.disabled"
                    :clearable="true"
                    :placeholder="item.label"
            ></el-date-picker>
        </el-form-item>
        <div class="searchBtn">
            <el-button-group>
                <el-button
                        v-for="(item, index) in formConfig.operate"
                        :key="index"
                        size="small"
                        :type="item.type"
                        :plain="item.plain"
                        :icon="item.icon"
                        :hxmodule="[formConfig.moduleName,item.logType,item.name]"
                        @click="item.handleClick"
                >{{item.name}}
                </el-button>
            </el-button-group>
            <slot name="operate"></slot>
        </div>
    </el-form>
</template>
<script>
    export default {
        components: {},
        props: {
            formConfig: {
                type: Object,
                required: true
            },
            value: {
                type: Object,
                required: true
            },
            rules: {
                type: Object
            },
            isInline: {
                type: Boolean,
                default: true,
            },
            plain:{
                type: Boolean
            }
        },
        computed: {},
        methods: {
            setDefaultValue() {
                const formData = {...this.value};
                // 
                this.formConfig.formItemList.forEach(({key, value}) => {
                    if (formData[key] === undefined || formData[key] === null) {
                        formData[key] = value;
                    }
                });
                this.$emit("input", formData);
            }
        },
        watch: {
            value: {
                deep: true,
                immediate: true,
                handler(val) {
                    this.value = val;
                }
            }
        },
        mounted() {
            this.setDefaultValue();
        }
    };
</script>
<style>
    .searchBtn {
        display: inline-block;
    }
</style>




