<template>
  <div>
    <el-container style="height: 100%">
        <el-main>
          <div class="search table-header-btn">
            <div class="table-header-btn-l"></div>
            <div class="table-header-btn-r">
              <el-button type="primary" size="small" @click="saveClick()"></el-button>
            </div>
          </div>
          <el-table 
            ref="multipleTable"
            :data="tableData" 
            @selection-change="selectionChangeHandle"
          >
              <el-table-column type="selection"  width="50" align="center"></el-table-column>
              <el-table-column width="50" fixed type="index" :index="indexMethod" label="" align="center"></el-table-column>
              <el-table-column prop="tabCode" label=""></el-table-column>
              <el-table-column prop="code" label=""></el-table-column>
              <el-table-column prop="name" label=""></el-table-column>
          </el-table>
        </el-main>
    </el-container>
  </div>
</template>
<script>
export default {
  props: ['checkedData'],
  data() {
    return {
        Height: {
            height: document.documentElement.clientHeight - 200 + "px"
        }, 
        tableData: [], //
        tableDataSelections:[]
    };
  },
  created() {
    this.getTitleData();
  },
  methods: {
      getTitleData(){
        this.$http({
          url: this.$http.adornUrl(this.API.getMingceRelateColumn),
          method: 'post',
          data: this.$http.adornData({
            mingceId:this.checkedData.id
          })
        }).then(({data}) =>{
            if(data.code == 0) {
              this.tableData = data.data;
              for ( let i = 0 ; i < this.tableData.length ; i++ ){
                if(this.tableData[i].isCheck == 1){
                  this.$nextTick( () => {
                    this.$refs.multipleTable.toggleRowSelection(this.tableData[i]);
                  })
                }else{
                  this.$refs.multipleTable.clearSelection();
                }
              }  
            } else {
              this.$message.error(data.msg);
            }
        })
      },
      indexMethod(index) {
        return index + 1;
      },
      selectionChangeHandle(row) {
        this.tableDataSelections = row;
      },
      saveClick() {
        if(this.tableDataSelections.length == 0){
          var sortArr = [];
          sortArr.push({
            mingceId:this.checkedData.id,
            colCode:"",
            tabCode:"",
          });
          this.$http({
            url: this.$http.adornUrl(this.API.saveMingceOrder),
            method: "post",
            data: this.$http.adornData({
              data: JSON.stringify(sortArr),
            })
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.$message.success("");
            } else {
              this.$message.error(data.msg);
            }
          });
        }else{
          var sortArr = [];
          this.tableDataSelections.forEach((item, index) => {
            sortArr.push({
              mingceId:this.checkedData.id,
              colCode:item.code,
              tabCode:item.tabCode,
            });
          });
          this.$http({
            url: this.$http.adornUrl(this.API.saveMingceOrder),
            method: "post",
            data: this.$http.adornData({
              data: JSON.stringify(sortArr),
            })
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.$message.success("");
            } else {
              this.$message.error(data.msg);
            }
          });
        }
        
      }
  },
 
};
</script>

<style lang="scss" scoped>
    .el-main {
        padding:0;
    }
</style>






