<template>
  <el-dialog
    class="grant-authorization"
    title=""
    :close-on-click-modal="false"
    :visible.sync="visible">
      <div class="grant-authorization-title">
        <span></span>
        <p>{{dataList.length}}</p>
      </div>
      <div class="float-right">
        <el-button type="primary" size="small" @click='saveHandle'></el-button>
      </div>
      <div class="clearfix"></div>
      <el-table
        ref='table'
        :data="dataList"
        border
        v-loading="dataListLoading"
        height="400px"
        style="width: 100%;"
        @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="40"></el-table-column>
        <el-table-column label="" type="index" width="50" align="center"></el-table-column>
        <el-table-column prop="ruleName" label=""></el-table-column>
      </el-table>
    </el-container>
  </el-dialog>
</template>

<script>
  export default{
    data(){
      return{
        visible:false,
        dataList:[],
        dataListLoading:false,
        multipleSelection:[],
        params:{},
        state:''
      }
    },
    methods:{
      init(data,type,tabId){
        this.visible=true;
        if(!type){ //
          this.dataList=data;
          this.$nextTick(()=>{
            this.dataList.forEach(row=> {
              if(row.isCheck==1) this.$refs.table.toggleRowSelection(row,true);
            });
          })
          this.state=false;
          this.params.tabId=tabId;
        }else{//
          this.params={schemeId:data,schemeType:type}
          this.getDataList()
          this.state=true;
        }
      },
      getDataList(){
        this.dataListLoading = true
        this.$http({
          url: this.$http.adornUrl(this.API.queryRelationRule),
          method: 'post',
          data: this.$http.adornData(this.params)
        }).then(({data}) => {
          if(data && data.code===0){
            this.dataList=data.data;
            this.$nextTick(()=>{
              this.dataList.forEach(row=> {
                if(row.choice==1) this.$refs.table.toggleRowSelection(row,true);
              });
            })
          }else{
            this.$message.error(data.msg);
            this.dataList=[];
          }
          this.dataListLoading = false
        });
      },
      handleSelectionChange(e){
        this.multipleSelection=[];
        e.forEach(item=>{
          this.multipleSelection.push(item.ruleId)
        })
      },
      saveHandle(){
        var parameData;
        if(this.state){
          parameData={
            schemeId:this.params.schemeId,
            data:this.multipleSelection.join(',')
          }
        }else{
          parameData={
            tabId:this.params.tabId,
            strAr:this.multipleSelection.join(',')
          }
        }
        this.$http({
          url: this.$http.adornUrl(this.API[this.state?'saveRelationRule':'saveDataChecktable']),
          method: 'post',
          data: this.$http.adornData(parameData)
        }).then(({data}) => {
          if(data && data.code===0){
            this.$message({
              message: '',
              type: 'success',
              duration: 1500,
              onClose: () => {
                this.$emit('refreshDataList')
              }
            })
          }else{
            this.$message.error(data.msg);
          }
        });
      }
    }
  }
</script>

<style lang="scss" scoped>
  .grant-authorization{
    &-title{
      float: left;
      padding-left: 10px;
      color: #f0992b;
      span{
        display: inline-block;
        width:10px;
        height: 10px;
        background: rgba(#f0992b,0.6);
        box-shadow: 0px 0px 6px 0px #f0992b;border-radius: 50%;}
      p{display: inline-block; padding-left: 10px;}
    }
    .float-right{
      padding-bottom: 10px;
    }
  }
</style>



