<template>
  <!--  -->
  <el-dialog
    class="grant-authorization"
    title=""
    :close-on-click-modal="false"
    :visible.sync="visible">
    <el-form class="float-right" :inline="true">
      <el-form-item>
        <el-button type="primary" size="small" @click="moveDirection('up')"></el-button>
        <el-button type="primary" size="small" @click="moveDirection('down')"></el-button>
        <el-button type="primary" size="small" @click="sortHanlde"></el-button>
      </el-form-item>
    </el-form>
    <el-table
      :data="dataList"
      border
      height="400px"
      v-loading="dataListLoading"
      @selection-change="selectionChangeHandle"
      style="width: 100%;">
      <el-table-column type="selection" width="40"></el-table-column>
      <el-table-column label="" type="index" width="50" align="center"></el-table-column>
      <el-table-column prop="b0101" label=""></el-table-column>
    </el-table>
  </el-dialog>
</template>

<script>
  import {swapItems} from '@/utils';
  export default{
    data(){
      return{
        visible:false,
        dataList:[],
        dataListLoading:false,
        dataListSelections:[]
      }
    },
    methods:{
      init(id){
        this.visible = true;
        this.$http({
          url: this.$http.adornUrl(this.API.getChildOrgB01),
          method: 'post',
          data: this.$http.adornData({
            b0100:id
          })
        }).then(({data}) => {
          this.dataList=[];
          if(data && data.code===0){
            data.data.forEach(item=>{
              this.dataList.push({
                b0101:item.b0101,
                b0100:item.b0100
              })
            })
          }else{
            this.$message.error(data.msg);
          }
        });
      },
      selectionChangeHandle(val) {//
        this.dataListSelections=val;
      },
      moveDirection(direction){ //
      	var that=this,index;
      	that.dataListSelections.sort((prev,next)=>{
      		return that.dataList.indexOf(prev)-that.dataList.indexOf(next)
      	})
      	if(direction=='down') that.dataListSelections.reverse()
      	that.dataListSelections.forEach(item=>{
      		index=that.dataList.indexOf(item); //
      		if(direction=='up') that.moveUp(index);
      		else if(direction=='down') that.moveDown(index)
      	})
      },
      moveUp(index){//
      	if(index==0) return;
      	this.dataList=swapItems(this.dataList,index,index-1,'up');
      },
      moveDown(index){ //
      	if(index==this.dataList.length-1) return;
      	this.dataList=swapItems(this.dataList,index,index+1,'down');
      },
      sortHanlde(){//
        var sortArr=[];
        if(this.dataList.length<0) return;
        this.dataList.forEach((item,index)=>{
          sortArr.push({
            b0100:item.b0100,
            hx001:index+1
          })
        })
        this.$http({
          url: this.$http.adornUrl(this.API.getChildOrgSort),
          method: 'post',
          data: this.$http.adornData({
            data:JSON.stringify(sortArr)
          })
        }).then(({data}) => {
          if(data && data.code===0){
            this.$message({
              message: '',
              type: 'success',
              duration: 1500,
              onClose: () => {
                this.$emit('refreshDataList')
              }
            })
          }else{
            this.$message.error(data.msg);
          }
        });
      },
    }
  }
</script>

<style scoped>
  .el-form--inline /deep/ .el-form-item{margin-bottom: 10px;}
</style>



