<template>
  <div>
    <!---->
    <div class="topbar clearfix0 scrollContainer" ref="scrollContainer" style="position: relative">
      <i v-show="lshow" class="el-icon-d-arrow-left left" @click="leftmove" ></i>
      <div ref="tabletopbar" class="table-top-bar"  :style="style">
        <div ref="tabletopbarl" class="foot-left">
          <!--<ul class="foot-left-ul">-->
            <!--<li><span class="bg-img"></span><span class="border-r" id="unitName">{{searchConfigs.currentOrgItem.name}}</span></li>-->
            <!--<li><span class="border-r" id="span_totalcount"><span id="spanTotalCount" style="display:inline-block; padding:0px 3px; text-align:center ">{{page.total}}</span></span></li>-->
          <!--</ul>-->
        </div>
        <div ref="tabletopbarr"  class="float-right" >

          <el-button type="primary" size="small" @click="statisticalEcharts"></el-button>

          <el-button type="primary" size="small" @click="checkout"></el-button>
          <el-button type="primary" size="small" @click="addPersonnelHandle"></el-button>
          <el-button type="primary" size="small" @click="deleteHandle"></el-button>
          <el-button type="primary" size="small" @click="peopleSort"></el-button>

          <el-button type="primary" size="small" @click="addList"></el-button>
          <!--<el-button type="primary" size="small"></el-button>-->
          <el-dropdown trigger="click"  @command="batchMaintenance">
            <el-button type="primary" size="small">
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item v-for="(item,index) in plwhOptions" :command="index" :key="index">{{item.label}}</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <el-dropdown trigger="click"  @command="batchMaintenance">
            <el-button type="primary" size="small">
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item v-for="(item,index) in bookFormsOptions" :command="index" :key="index">{{item.label}}</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <el-dropdown trigger="click"  @command="batchMaintenance">
            <el-button type="primary" size="small">
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item v-for="(item,index) in exportOptions" :command="index" :key="index">{{item.label}}</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <el-button type="primary" size="small" @click="setDisplay" style="margin-left: 10px;"></el-button>
        </div>
      </div>
      <i v-show="rshow" class="el-icon-d-arrow-right right" @click="rightmove"></i>
      <!---->
      <checkout-scheme ref="checkoutScheme"></checkout-scheme>
      <!---->
      <add-personnel v-if="addPersonnelVisible" ref='addPersonnel' @updatDataList="updatDataList"></add-personnel>
      <!---->
      <statistical-echarts ref="statisticalEcharts"></statistical-echarts>
      <!---->
      <people-sort ref="peopleSort" :tableData="tableData"></people-sort>
      <!---->
      <batch-year-assessment ref="batchYearAssessment"></batch-year-assessment>
      <!---->
      <add-list ref="addList"></add-list>
      <!---->
      <setDisplay ref="setDisplay" :selectedData="selectedData" @getDataList="getDataList"></setDisplay>


    </div>

    <!---->


<div :style="{height:height}" v-loading="dataListLoading" element-loading-background="rgba(255, 255, 255, 0)">
  <el-table
    v-if="tableShow && selectedData.componentsType == '0'"
    ref="eltable"
    :data="tableData.info"
    border
    v-loading="dataListLoading"
    element-loading-background="rgba(255, 255, 255, 0)"
    :height="height"
    style="width: 100%;">

    <template v-for="(item,index) in tableData.cols">
      <el-table-column
        type="selection"
        width="55"
        v-if="index == 0">
      </el-table-column>
      <el-table-column
        width="50px"
        type="index"
        :index="indexMethod"
        label=""
        align="center"
        v-if="index == 0"
      ></el-table-column>

      <template  v-if="item != null">
        <el-table-column v-if="item.colCode == 'A0101'"  sortable :align="item.listAlign"  :prop="item.colCode" :width="item.listWidth" :label="item.colName" :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <span class="nameSty" @click="openLeaderInfo(scope.row)">{{scope.row.A0101}}</span>
          </template>
        </el-table-column>
        <el-table-column v-else  :align="item.listAlign" :prop="item.colCode" :width="item.listWidth" :label="item.colName" :show-overflow-tooltip="true"></el-table-column>
      </template>
    </template>
  </el-table>

  <div  v-show="selectedData.componentsType == '1'">
    <div :style="{'height':height}" style="width: 100%; overflow: auto; display: block;" id="pictureView" class="pictureView">
      <span v-if="tableData.info == undefined || tableData.info.length == 0" class="nospan"></span>
      <ul id="pictureViewUl" v-else>
        <el-checkbox-group v-model="checkList">
          <li v-for="(item,index) in tableData.info" class="" :key="index" :style="liStyle">
            <div class="pictureBox clearfloat">
              <div class="photo" :style="imgStyle">
                <img :src="item.A5714==undefined?noPic:(host+'/photos/'+item.A5714+'?token='+token)"   @click.stop="openLeaderInfo(item)">
              </div>
              <div class="introduction" :style="introductionStyle">
                <h5>
                  <el-checkbox    :label="item.A0100">{{item.A0101}}</el-checkbox>
                </h5>
                <p :style="pStyle" title="196310198108201709202004">196310198108201709202004</p>
              </div>
            </div>
          </li>
        </el-checkbox-group>
      </ul>
    </div>
  </div>
</div>




    <pagination ref="pagination" :page="page" @pageChange="pageChange" @pageChangeSize="pageChangeSize" @viewChange="viewChange" :selectIsShow="selectIsShow"></pagination>

    <!---->
    <leader-info-index ref="leaderInfoIndex" :info="tableData.info"></leader-info-index>
  </div>
</template>

<script>
  import pagination from '@/components/baseComponents/newPagination'
  import leaderInfoIndex from '../leaderInfo/leaderInfoIndex'
  import addPersonnel from '../add-personnel.vue'
  import statisticalEcharts from './statisticalEcharts'
  import batchYearAssessment from './batchYearAssessment'
  import checkoutScheme from './checkoutScheme'
  import setDisplay from './setDisplay'
  import peopleSort from './peopleSort'
  import addList from './addList'
    export default {
        name: "perMainTable",
        components:{
            leaderInfoIndex,pagination,addPersonnel,statisticalEcharts,batchYearAssessment,setDisplay,checkoutScheme,peopleSort,addList
        },
        props:['searchConfigs','siteContentViewHeight','page','conditionCheckbox','conditionName'],
        data() {
            return {
                tableData:[],
                dataListLoading:true,
                selectIsShow:true,
                selectedData:{
                  componentsDesc: "",
                  componentsId: "4030C2A0-2FD5-4127-BB1B-F34E60F2BACF",
                  componentsName: "",
                  componentsType: "0",
                  createtime: "2020-04-28 11:36:12",
                  createuser: "admin",
                  modifytime: "2020-05-29 11:47:19",
                  modifyuser: "admin",
                  orderno: 1,
                  tabId: "0416DD56-CFC4-4CDD-8177-015E7C874AD6"
                },
                height:null,
                addPersonnelVisible:false,
                tableShow:false,
                plwhOptions:[
                  {
                    value:'0',
                    label:''
                  },{
                    value:'1',
                    label:''
                  }
                ],
                bookFormsOptions:[
                  {
                    value:'0',
                    label:''
                  },{
                    value:'1',
                    label:''
                  },{
                    value:'2',
                    label:''
                  },{
                    value:'3',
                    label:''
                  },{
                    value:'4',
                    label:''
                  }
                ],
                exportOptions:[
                  {
                    value:'0',
                    label:'A4'
                  },{
                    value:'1',
                    label:'B5'
                  },{
                    value:'2',
                    label:'A4'
                  },{
                    value:'3',
                    label:'B5'
                  },{
                    value:'4',
                    label:'Lrmx'
                  },{
                    value:'5',
                    label:''
                  }
                ],
                checkList:[],
                noPic:require("@/assets/img/no_pic.jpg"),
                checked:'checked',
                imgStyle:{},
                introductionStyle:{},
                pStyle:{},
                liStyle:{},
                token:this.token,
                host:this.$host,
                cols:{
                  photoWidth:'',
                  photoHeight:'',
                  photoWidthrow:'',
                  photoHeightrow:'',
                  photoShowrows:'',
                },
                selectionArr:[],
                step:80,
                cur: 0,
                chaVal:'',
                style:null,
                lshow:false,
                rshow:false,
                screenWidth:'',
            }
        },
        mounted () {
          const that = this
          window.addEventListener('resize', () => {
            return (() => {
              window.screenWidth = document.body.clientWidth
              that.screenWidth = window.screenWidth
              that.$nextTick(function(){
                that.scroll()
              })
            })()
          }, false)
        },
        created(){
            let token =  sessionStorage.getItem('token')
            this.token= token.replace("\"","").replace("\"","")
            this.getDataList()
            this.$nextTick(function(){
              this.height = this.siteContentViewHeight - 58 -48 - this.searchConfigs.searchAreaHeight  +'px'
              this.scroll()
            })
        },
        watch:{
          selectedData:{
            handler(newName, oldName) {
              // this.getDataList()
            },
            immediate: true,
            deep: true
          },
          searchConfigs:{
            handler(newName, oldName) {
             this.$nextTick(function(){
                this.height = this.siteContentViewHeight - 58 -48 - this.searchConfigs.searchAreaHeight+'px'
              })
            },
            // wacthfirstNamehandler
            deep: true
          },
          siteContentViewHeight(){
            this.height = this.siteContentViewHeight - 58 -48 - this.searchConfigs.searchAreaHeight+'px'
          },
          screenWidth(val){
            this.screenWidth = val
            this.$nextTick(function(){
              this.scroll()
            })
          }
        },
        methods: {
            updatDataList(){
              //
              this.page.pageIndex = 1
              // this.tableShow = false 
              this.getDataList()
            },
            getDataList(){
              this.dataListLoading = true


              //
              let viewId = this.selectedData.componentsId
              let viewType = this.selectedData.componentsType
              let orgId = this.searchConfigs.orgId

              let whereExpressions = []
              if(orgId.length == 0){
                whereExpressions = []
              }else{
                let orgObj = {
                  connector: "and",
                  fieldName: "b01.b0111",
                  value: orgId,
                }
                if(this.searchConfigs.multiAgencyQuery){
                  //
                  orgObj.operator = "in"
                  orgObj.expressionPrefix="("
                  orgObj.expressionSuffix=")"
                }else{
                  //
                  orgObj.operator = "="
                  orgObj.expressionPrefix=""
                  orgObj.expressionSuffix=""
                }
                whereExpressions.push(orgObj)
              }


              //
              this.conditionCheckbox.forEach(function(item,index){
                let checkListLength = item.conditionCheckList.length
                if(checkListLength !== 0){
                  let orgObj = {
                    connector: "and",
                    expressionPrefix: "",
                    expressionSuffix: "",
                    fieldName: item.itemname,
                    value: item.conditionCheckList,
                  }
                  if(item.conditionCheckList.length == 1 ){
                    //
                    orgObj.operator = "="
                  }else{
                    //
                    orgObj.operator = "in"
                  }
                  whereExpressions.push(orgObj)
                }
              })

              let name = this.conditionName.name
              name = name.replace(/\s*/g,'')
              if(name !== ''){
                let perName = {
                  Connector: "AND",
                  ExpressionPrefix: "((",
                  ExpressionSuffix: "))",
                  FieldName: "a01.a0101",
                  Operator: "Like",
                  ParamterName: "a01.a0101",
                  PropertyCName: "",
                  Value: `%${name}%`,
                  ValueTxt: `%${name}%`
                }
                whereExpressions.push(perName)
              }

                //
              console.log(this.searchConfigs.nameListSelect)
              let nameListSelect = this.searchConfigs.nameListSelect
              if(nameListSelect){
                let nameList = {
                  FieldName: "NameListPersonMapper.NameListId",
                  Operator: "Equals",
                  ParamterName: "NameListPersonMapper_NameListId",
                  PropertyCName: "",
                  Value: nameListSelect.listId,
                  ValueTxt: ""
                }
                whereExpressions.push(nameList)
              }else{
                console.log('22')
              }


                var params = {
                  fileds: [],
                  isNoJob: 0,
                  current: this.page.pageIndex,
                  size: this.page.pageSize,
                  viewId: viewId,
                  viewType:viewType,
                  whereExpressions:whereExpressions
                }
                let b =JSON.stringify(params)
                this.$http({
                  headers: {
                    'Content-Type': 'application/json;charset=UTF-8'
                  },
                  url: this.$http.adornUrl(this.API.getPersonInformation),
                  method: 'post',
                  data: params
                }).then(({data}) => {
                  if(data && data.code===0){
                      this.tableData = data.data
                      this.page.total = data.data.total
                      //
                      if(this.selectedData.componentsType == '1'){
                        this.cols = this.tableData.cols
                        this.imgStyle={
                          width:this.cols.photoWidth+'px',
                          height:this.cols.photoHeight+'px',
                        }
                        this.introductionStyle={
                          left:this.cols.photoWidth+'px'
                        }
                        this.pStyle={
                          '-webkit-line-clamp':this.cols.photoShowrows
                        }
                        this.liStyle={
                          width:this.cols.photoWidthrow+'%',
                          height:this.cols.photoHeightrow+'px'
                        }
                      }
                      this.$emit('updatePage',this.page)
                      this.$nextTick(function(){
                        this.height = this.siteContentViewHeight - 58 -48 - this.searchConfigs.searchAreaHeight+'px'
                        this.tableShow = true
                        this.dataListLoading = false
                      })
                  }else{
                      this.$message.error(data.msg);
                  }

                });

            },
            openLeaderInfo(row){
              let id = row.A0100
              function objFn(obj, objIndex, objs){
                return obj.A0100 == id;
              }
              let rowIndex = this.tableData.info.findIndex(objFn)
              //
              this.$refs.leaderInfoIndex.initDialog(rowIndex)
            },
            viewChange(val){
              //
              this.selectedData = val
              this.tableShow = false
              this.getDataList()
            },
            indexMethod(index) {
              //
              return index + 1;
            },
            checkout(){
              this.$refs.checkoutScheme.initDialog()
            },
            addPersonnelHandle(){
              //
              this.addPersonnelVisible=true;
              this.$nextTick(() => {
                this.$refs.addPersonnel.init()
              })
            },
            statisticalEcharts(){
             //
              this.$refs.statisticalEcharts.initDialog()
            },
            //
            deleteHandle(){
              let ids = ''
              let componentsType = this.selectedData.componentsType
              if(componentsType == '0'){
                //
                //
                this.selectionArr = this.$refs.eltable.selection
                if(this.selectionArr.length == 0){
                  this.$message({
                    type: 'warning',
                    message: '!'
                  });
                }else {
                  this.selectionArr.forEach(function (item, index) {
                    ids += item.A0100
                    ids += ','
                  })
                  this.deleteFn(ids)
                }
              }else if(componentsType == '1'){
                //
                if(this.checkList.length == 0){
                  this.$message({
                    type: 'warning',
                    message: '!'
                  });
                }else {
                  this.checkList.forEach(function (item, index) {
                    ids += item
                    ids += ','
                  })
                  this.deleteFn(ids)
                }
              }
            },
            deleteFn(ids){
              let param  = {
                ids:ids
              }
              this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
              }).then(() => {
                this.$http({
                  url: this.$http.adornUrl(this.API.deletePersonInformation),
                  method: 'post',
                  data: this.$http.adornData(param)
                }).then(({data}) => {
                  if(data.code===0){
                    this.$message({
                      type: 'success',
                      message: '!'
                    });
                    if(this.selectionArr == undefined){
                      this.selectionArr = []
                    }
                    //
                    //
                    if(this.tableData.info.length == this.selectionArr.length || this.tableData.info.length == this.checkList.length){
                      if(this.page.pageIndex == 1){
                        this.page.pageIndex = 1
                      }else{
                        this.page.pageIndex =  this.page.pageIndex -1
                      }
                      this.tableShow = false
                    }else{
                      this.tableShow = true
                    }
                    this.getDataList()
                  }else{
                    this.$message.error(data.msg);
                  }
                });

              }).catch(() => {
                this.$message({
                  type: 'info',
                  message: '!'
                });
              });
            },
            peopleSort(){
              //
              this.$refs.peopleSort.initDialog()
            },
            batchMaintenance(command){
              //
              if(command == 0){
                //
                this.$refs.batchYearAssessment.initDialog()
              }else if(command == 1){
                this.selectionArr = this.$refs.eltable.selection
                console.log(this.selectionArr)
                //
                if(this.selectionArr.length == 0){
                  this.$message({
                    type: 'warning',
                    message: '!'
                  });
                }else{
                  this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning',
                  }).then(() => {
                    this.$message({
                      type: 'success',
                      message: ''
                    });
                  }).catch(() => {
                    this.$message({
                      type: 'info',
                      message: ''
                    });
                  });
                }
              }

            },
            addList(){


              let ids = ''
              let componentsType = this.selectedData.componentsType
              if(componentsType == '0'){
                //
                //
                this.selectionArr = this.$refs.eltable.selection
                if(this.selectionArr.length == 0){
                  this.$message({
                    type: 'warning',
                    message: '!'
                  });
                }else {
                  this.selectionArr.forEach(function (item, index) {
                    ids += item.A0100
                    ids += ','
                  })
                  //
                  this.$refs.addList.initDialog(ids)
                }
              }else if(componentsType == '1'){
                //
                if(this.checkList.length == 0){
                  this.$message({
                    type: 'warning',
                    message: '!'
                  });
                }else {
                  this.checkList.forEach(function (item, index) {
                    ids += item
                    ids += ','
                  })
                  //
                  this.$refs.addList.initDialog(ids)
                }
              }
            },
            setDisplay(){
               //
               this.$refs.setDisplay.initDialog()
            },
            scroll(){
              this.$nextTick(function(){
                this.style="transform:translateX(0);width:2000px"
                var scrollContainerWidth
                if(this.$refs.scrollContainer != undefined){
                  scrollContainerWidth =  this.$refs.scrollContainer.clientWidth;
                }
                // console.log(scrollContainerWidth)
                // let left= this.$refs.tabletopbarl.clientWidth
                // var contentWidth = this.$refs.tabletopbarl.clientWidth + this.$refs.tabletopbarr.clientWidth + 2+44;//1pxfont-weight:bold;
                var contentWidth = 913 + 44 ;
                if(scrollContainerWidth < contentWidth){
                  this.style= { padding:'0 22px',width:contentWidth+'px'}
                  this.chaVal = contentWidth - scrollContainerWidth;
                  this.lshow = true
                  this.rshow = true
                }else{
                  this.style= { padding:0,width:'auto'}
                  this.lshow = false
                  this.rshow = false
                }
              })


            },
            leftmove() {
              this.cur =this.cur + this.step;
              if(this.cur > 0){
                this.cur = 0;
              }
              this.$set(this.style,'transform','translateX('+this.cur+'px)')
            },

            rightmove() {
              this.cur = this.cur - this.step;
              console.log(this.cur,this.chaVal)
              if(this.cur < -this.chaVal){
                this.cur =  - this.chaVal;
              }
              this.$set(this.style,'transform','translateX('+this.cur+'px)')
              // this.style = {'width':'100px'}
            },
            pageChange(val){
              //
              this.page.pageIndex=val
              this.tableShow = true
              this.getDataList()
            },
            pageChangeSize(val){
              //
              this.page.pageSize=val
              this.tableShow = true
              this.getDataList()
            },
        }
    }
</script>

<style lang="scss" scoped>
.nameSty{
  color: #0090F7;
  cursor: pointer;
}
.el-main.rightContent{
  position: relative;
}
  .clearfix{
    position: absolute;
    left: 304px;
    right: 18px;
    bottom: 0;
    background: #fff;
  }

.clearfix0:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}


/**/
.pictureView {
  overflow: auto;
  display: none;
  padding-top: 10px;
  position: relative;
}
.pictureView ul {
  /*width: 1260px;*/
  width: 100%;
  float: left;
  padding: 0 5px;
  box-sizing: border-box;
}
ul, li {
  list-style: none;
}
.pictureView ul li {
  padding: 0;
  /*width: 416px;*/
  width: 50%;
  height: 160px;
  float: left;
  padding: 5px;
  box-sizing: border-box;
  background-color: #fff;
}
img {
  display: block;
  border: none;
}
.pictureView ul .pictureBox {
  box-sizing: border-box;
  height: 100%;
  border: 1px solid #ccc;
  background-color: #ebf2fc;
  position: relative;
}
