  width: 110px;
  height: 146px;
  float: left;
  background: #fff;
}
.pictureView ul li .introduction {
  height: 146px;
  float: left;
  margin-top: 6px;
  margin-left: 10px;
  margin-right: 10px;
  position: absolute;
  left: 110px;
  top: 0;
  right: 0;
  bottom: 0;
}
.pictureView ul li .photo img {
  /*width: 110px;*/
  /*height: 146px;*/
  cursor: pointer;
  /*object-fit: cover;*/
  width: 100%;
  height: 100%;
}
.pictureView ul li .introduction h5,p {
  padding: 0;
  margin: 0;
}
.pictureView ul li .introduction p {
  padding-top: 15px;
  color: #3e454f;
  line-height: 20px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-indent: 2em;
}
/*checkbox*/
>>> .pictureView ul .el-checkbox__inner{
  border: 1px solid #353535;
}
>>> .pictureView ul .el-checkbox__label{
  font-weight: bold;
}
>>>.pictureView ul .el-checkbox__input.is-checked .el-checkbox__inner,>>>.pictureView ul .el-checkbox__input.is-indeterminate .el-checkbox__inner {
  background-color: #3E8EF7;
  border-color: #3E8EF7;
}
>>>.pictureView .el-checkbox-group{
  font-size: 14px;
}

  .left{
    height: 37px;
    position: absolute;
    left: 0px;
    top: 0px;
    z-index: 99;
    display: block;

    background: #e1dcff;
    height: 100%;
    align-items: center;
    display: flex;
    /*border: 1px solid #00a1ff;*/
    cursor: pointer;
    border-right: 1px solid #dcdfe6;
  }
  .right{
    height: 37px;
    position: absolute;
    right: 0px;
    top: 0px;
    z-index: 99;
    display: block;

    background: #e1dcff;
    height: 100%;
    align-items: center;
    display: flex;
    /*border: 1px solid #00a1ff;*/
    cursor: pointer;
    border-left: 1px solid #dcdfe6;
  }
  .topbar{
    border-bottom: 1px solid #dcdfe6;
  }
.table-top-bar{
  height: 58px;
  transition: transform 0.3s;
}
