<template>
  <div>
    <el-select v-model="value" placeholder="" class="viewSelect" v-show="selectShow">
      <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
      </el-option>
    </el-select>

    <div class="pagination-con">
      <el-pagination v-if="isSmall" small @size-change="handleSizeChange" @current-change="handleCurrentChange" layout="total,prev, pager, next" :page-size="pageObj.pageSize" :total="pageObj.total">
      </el-pagination>
      <el-pagination v-else @size-change="handleSizeChange" @current-change="handleCurrentChange" layout="total, sizes, prev, pager, next, jumper" :page-sizes="pageObj.pageSizes" :current-page="pageObj.pageIndex" :page-size="pageObj.pageSize" :total="pageObj.total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
export default {
  name: 'pagination',//
  props: ['pageIndex', 'pageSize', 'pageSizes', 'total', 'isTotal', 'enableSmall', 'selectIsShow'],
  computed: {
    selectShow:
      function () {
        if (this.selectIsShow == undefined) {
          return false
        } else {
          return true
        }
      }
  },
  data () {
    return {
      isSmall: this.enableSmall || false,//small
      pageObj: {
        pageIndex: this.pageIndex * 1 + 1,//
        pageSize: this.pageSize * 1,//
        pageSizes: this.pageSizes,
        total: this.total * 1//
      },
      options: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }],
      value: '1'
    }
  },

  methods: {
    /*
     * pageSize 
     */
    handleSizeChange (page_size) {
      this.pageObj.pageSize = page_size;
      this.pageObj.pageIndex = 1;
      //
      this.$emit('PageTurning', {
        page_index: this.pageObj.pageIndex * 1 - 1,//
        page_size: this.pageObj.pageSize//
      });
    },
    /*
     * currentPage 
     */
    handleCurrentChange (curr_page) {
      this.pageObj.pageIndex = curr_page;
      //
      this.$emit('PageTurning', {
        page_index: this.pageObj.pageIndex * 1 - 1,
        page_size: this.pageObj.pageSize
      });
    }
  },
  watch: {
    /**
     * 
     */
    pageSize: {
      deep: true,
      handler: function (currVal, prevVal) {
        this.pageObj.pageSize = currVal;
      }
    },
    /**
     * 
     */
    total: {
      deep: true,
      handler: function (currVal, prevVal) {
        this.pageObj.total = currVal;
      }
    },
    /**
     * 
     */
    pageIndex: {
      deep: true,
      handler: function (currVal, prevVal) {
        this.pageObj.pageIndex = currVal * 1 + 1;
      }
    }
  }
}
</script>
<style lang="scss">
.pagination-con {
  height: 100%;
  float: right;
  .el-pagination {
    margin: 0 !important;
    padding: 10px 10px 10px 0 !important;
  }
}
.viewSelect {
  margin-top: 7px;
  margin-left: 10px;
}
</style>



