<script>
  import elTreeSelect from "@/components/baseComponents/elTreeSelect";
  import selectPic from "./perInfoTable/selectPic"
  import orgTreeSelect from '@/components/baseComponents/orgTreeSelect'
  export default{
    components:{
      elTreeSelect,selectPic,orgTreeSelect
    },
    data(){
      return{
        show:false,
        visible:true,
        formData:{a0101:'',a0104:'',a0101a:'',A0181:'',a0107:'',a0107:'',a0117:'',a0111:'',a0111a:'',A0114:'',a0114a:'',a0127:'',a0131:'',A0134:'',a0187:'',a0184:'',a0151:'',a0157:'',a0201b:'',a0201c:'',a0215a:'',a0217:'',a0222b:'',a0221:'',a0243:'',a0245:'',a0229:'',a0289:'',a0251:'',a0249:'',a0219b:'',a0255:'',a0276:'',A0277:'',A0278:'',a0279:'',a0131:'',},
        // formData:{
        //   a01:{
        //     a0101:'',a0102:'',a0103:'',a0104:'',a0105:'',a0106:'',a0107:'',a0104:'',a0101a:'',A0181:'',a0107:'',a0117:'',a0111:'',a0111a:'',A0114:'',a0114a:'',a0127:'',a0131:'',A0134:'',a0187:'',a0184:'',a0151:'',a0157:'',
        //   },
        //   a02:{
        //     a0201b:'',a0201c:'',a0215a:'',a0217:'',a0222b:'',a0221:'',a0243:'',a0245:'',a0229:'',a0289:'',a0251:'',a0249:'',a0219b:'',a0255:'',a0276:'',A0277:'',A0278:'',a0279:''
        //   }
        // },
        props:{                // 
          value: 'itemCode',
          label: 'itemName',
          children: 'children',
          disabled:false,
          placeholder:''
        },
        isClearable:false,      // 
        isAccordion:true,      // 
        valueId:'',            // ID
        imageUrl: require("@/assets/img/no_pic.jpg"),
        dicCodeArr:[],
        counter:0,
        rules:{
          a0101:{ required: true, message: '', trigger: 'blur' },
          a0104:{ required: true, message: '', trigger: 'change' },
          a0107:{ required: true, message: '', trigger: 'blur' },
          a0117:{ required: true, message: '', trigger: 'change' },
          a0111:{ required: true, message: '', trigger: 'blur' },
          a0114a:{ required: true, message: '', trigger: 'blur' },
          a0127:{ required: true, message: '', trigger: 'blur' },
          A0134:{ required: true, message: '', trigger: 'blur' },
          a0157:{ required: true, message: '', trigger: 'blur' },
          a0102:{ required: true, message: '', trigger: 'blur' },
          a0105:{ required: true, message: '', trigger: 'blur' },
          a0201b:{ required: true, message: '', trigger: 'change' },
        },
        file:null
      }
    },
    computed:{
      documentClientHeight: {
        get () { return this.$store.state.common.documentClientHeight }
      }
    },
    created(){
      this.getData()
      this.file = null
    },
    methods:{
      init(){
        this.visible = true;
        this.$refs.formData.resetFields();
        this.imageUrl = require("@/assets/img/no_pic.jpg")
      },
      selectPic(){
        //
        this.$refs.selectPic.initDialog()
      },
      getFile(val){
        //
        this.file = val
        if(this.file == ''){
          this.imageUrl = require("@/assets/img/no_pic.jpg")
        }else{
          this.imageUrl = URL.createObjectURL(this.file.raw);
        }
      },
      getData(){
        //
        let a =  [{
          dicCode:'ZB19',
          controlType:'4'
        },{
          dicCode:'ZB65',
          controlType:'4'
        },{
          dicCode:'GB3304',
          controlType:'4'
        },{
          dicCode:'GB2261',
          controlType:'4'
        },{
          dicCode:'GB2261C',
          controlType:'4'
        },{
          dicCode:'JRDWFS',
          controlType:'4'
        },{
          dicCode:'ZB130',
          controlType:'4'
        },{
          dicCode:'HangsSF01',
          controlType:'4'
        },{
          dicCode:' ZB12 ',
          controlType:'4'
        },{
          dicCode:'ZB42',
          controlType:'4'
        },{
          dicCode:'ZB14',
          controlType:'4'
        },{
          dicCode:'ZB129',
          controlType:'4'
        }]
        let This = this
        let ah = a.length
        //form
        if(ah == 0){
          this.show = true
          this.$nextTick(function(){
            this.loading = false
          })
        }else{
          a.forEach(function(item,index){
            //
            This.getDicCodeItem(item,ah)
          })
        }
      },
      getDicCodeItem(item,ah){
        let This = this
        //
        let dicCode = item.dicCode
        let controlType = item.controlType
        var params = {
          dictCode:dicCode
        }

        if(controlType == '13'){
          this.$http({
            url: this.$http.adornUrl(this.API.getOrganizationTree),
            method: 'post',
            data: this.$http.adornData(params)
          }).then(({data}) => {
            if(data && data.code===0){
              this.dicCodeArr[item.dicCode]=data.data

              this.counter= This.counter +1
              if(this.counter === ah){
                this.show = true

                this.$nextTick(function(){
                  this.loading = false
                })
              }
            }else{
              this.$message.error(data.msg);
            }
          });
        }else{
          this.$http({
            url: this.$http.adornUrl(this.API.getDictItemByCode),
            method: 'post',
            data: this.$http.adornData(params)
          }).then(({data}) => {
            if(data && data.code===0){
              this.dicCodeArr[item.dicCode]=data.data

              this.counter= This.counter +1
              if(this.counter === ah){
                this.show = true
                this.$nextTick(function(){
                  this.loading = false
                })
              }

            }else{
              this.$message.error(data.msg);
            }
          });
        }
      },
      selectOrg(){
        this.$refs.orgTreeSelect.initDialog()
      },
      getSelectTreeData(data){
        this.formData.a0201b =  data.name
      },
      save(){
        this.$refs.formData.validate((valid,notPassObj) => {
          console.log(notPassObj)
          if (valid) {
            this.submit()
          } else {
            let str = ''
            for(var key in notPassObj){
              let item = notPassObj[key]
              let msg = item[0].message
              msg = msg.substring(0,msg.length-4);
              str += `<div style="display:flex;align-items:center;margin-bottom:4px;"><i class="el-icon-warning-outline iconsty" style="color:#E5463A;font-size:20px;"></i><span style="font-size:16px;"><span style="color: #498ef7;">${msg}</span></span></div>`
            }
            this.$alert(str, '', {
              dangerouslyUseHTMLString: true
            });
            console.log('error submit!!');
            return false;
          }

        })
      },
      submit(){
        //
        //
        let formData = new FormData()
        if(this.file !== undefined){
          formData.append("file", this.file.raw);
        }

        //

        let data={
          a01:{
            a0101:this.formData.a0101,
            a0103:this.formData.a0103,
            a0104:this.formData.a0104,
            a0101a:this.formData.a0101a,
            A0181:this.formData.A0181,
            a0107:this.formData.a0107,
            a0117:this.formData.a0117,
            a0111:this.formData.a0111,
            a0111a:this.formData.a0111a,
            A0114:this.formData.A0114,
            a0114a:this.formData.a0114a,
            a0127:this.formData.a0127,
            a0131:this.formData.a0131,
            A0134:this.formData.A0134,
            a0187:this.formData.a0187,
            a0184:this.formData.a0184,
            a0151:this.formData.a0151,
            a0157:this.formData.a0157,
          },
          a02:{
            a0201b:this.formData.a0201b,
            a0201c:this.formData.a0201c,
            a0215a:this.formData.a0215a,
            a0217:this.formData.a0217,
            a0222b:this.formData.a0222b,
            a0221:this.formData.a0221,
            a0243:this.formData.a0243,
            a0245:this.formData.a0245,
            a0229:this.formData.a0229,
            a0289:this.formData.a0289,
            a0251:this.formData.a0251,
            a0249:this.formData.a0249,
            a0219b:this.formData.a0219b,
            a0255:this.formData.a0255,
            a0276:this.formData.a0276,
            A0277:this.formData.A0277,
            A0278:this.formData.A0278,
            a0279:this.formData.a0279,
          }
        }

        let jsondata = JSON.stringify(data)
        formData.append("data", jsondata);

        // let a01 = JSON.stringify(this.formData.a01)
        // formData.append("a01", a01);
        // let a02 = JSON.stringify(this.formData.a02)
        // formData.append("a02", a02);

        this.$http({
          headers: {
            'Content-Type': 'multipart/form-data;'
          },
          url: this.$http.adornUrl(this.API.addPersonInformation),
          method: 'post',
          data: formData
        }).then(({data}) => {
          if(data && data.code===0){
            this.visible = false
            this.$emit('updatDataList')
            this.$message({
              message: '',
              type: 'success'
            });
          }else{
            this.$message.error(data.msg);
          }
        })
      },

    }
  }
</script>
<style lang="scss" scoped>
  .add-personnel{
    /deep/ .el-dialog__body{
      padding-right: 0;padding-left: 0;
      .float-right{margin-right: 10px;}
    }
    &-ul{
      padding-left: 0;
      &-title{background: #f3f3f3;color: #f0992b;padding: 0 30px;font-size: 16px;line-height: 42px;border-bottom: 1px solid #ededed;margin-bottom: 16px;}
      .el-form{
        padding: 20px;
        .el-form-item{
          border:1px solid #D1CECE;
          margin: 0 4px 16px;
          /deep/ .el-form-item__label{padding-left: 6px;border-right: 1px solid #D1CECE;background: #F9F9F9;}
          /deep/ .el-input__inner{border: none}
        }
        .upload-img-form{
          height: 230px;
          .el-form-item__content{
            margin-left: 0!important;
          }
        }
      }
    }
  }

  >>> .el-header{
    height: 40px!important;
    line-height: 40px;
  }
  .add-personnel-ul{
    margin: 0;
  }

  .demo-block .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .demo-block .avatar-uploader .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }

  /**/
  .avatar{
    width: 100%;
    height: 100%;
  }
  >>>.demo-block .el-form-item__content{
    margin-left: 0!important;
    /*height: 100%;*/
    height: 251px;
  }
  >>>.demo-block .avatar-uploader{
    height: 100%;
  }
  >>>.demo-block  .el-upload,>>>.demo-block  .el-icon-plus{
    width: 100%!important;
    height: 100%!important;
  }
  >>>.demo-block .avatar-uploader-icon{
    line-height: 224px!important;
  }
  >>>.commonsty-form .el-form-item {
    margin: 0 4px 16px;
  }

</style>



