<template>
  <div ref="leftcontent" style="height:100%;box-sizing:border-box;overflow:hidden;">
    <!---->
    <div class="divPhotoWithDesc">
      <div class="img-box" ref="imgBox">
        <img :src="src" @click="selectPicture"/>
      </div>
      <div class="img-msg" ref="imgMsg">
        <span style="font-weight: bold;">{{data.name}}</span>
        <!--<span></span>-->
        <span>{{data.posi}}</span>
      </div>
    </div>
    <!---->
    <div class="popup-main-level" ref="popup-main-level" :style="{'height':height}">

      <el-row>
        <el-col>
          <el-menu
            class="el-menu-vertical"
            :unique-opened="true"
            background-color="#fbfbfb"
            :default-active="activeMenu">
            <el-submenu v-for="(item,index) in secondArr" :key="item.id" :index="item.id" >
              <template slot="title">
                <span class="level-list"></span>
                <span>{{item.name}}</span>
              </template>
              <el-menu-item  v-for="(item1,index1) in item.children" :key="item1.id" :index="item1.id" @click="selectMenu(item1)">
                <span class="secondary-list"></span>
                <span>{{item1.name}}</span>
              </el-menu-item>
            </el-submenu>
          </el-menu>
        </el-col>
      </el-row>
    </div>


    <!---->
    <select-picture ref="selectPicture" :personId="personId"></select-picture>

  </div>
</template>

<script>
    import selectPicture from './informationTools/selectPicture'
    export default {
        name: "informationList",
        components:{
          selectPicture
        },
        props:{
            secondArr:{
                type:Array,
                required: true
            },
            personId:{
              required: true
            }
        },
        watch:{
          personId:{
            handler(val){
              console.log(val)
              this.getInfor()
            }
          }
        },
        data() {
            return {
              src:require("@/assets/img/no_pic.jpg"),
              noPic:require("@/assets/img/no_pic.jpg"),
              activeMenu:this.secondArr[0].children[0].id,
              data:{},
              tokenstr:'',
              height:null
            }
        },
        created(){
         // 
         console.log(this.activeMenu)
         let item = this.secondArr[0].children[0]
         this.activeMenu = this.secondArr[0].children[0].id
         this.$emit('switchComponent',item)

         this.token =  sessionStorage.getItem('token')
         this.getInfor()

        },
        methods: {
            getInfor(){
              var params = {
                a0100: this.personId,
              }
              this.$http({
                url: this.$http.adornUrl(this.API.getPhotoAndPosi),
                method: 'post',
                data:  this.$http.adornData(params)
              }).then(({data}) => {
                if(data && data.code===0){
                  this.data = data.data
                  if(this.data.zhaoPianPath !== ''){
                    let token = this.token
                    this.tokenstr= token.replace("\"","").replace("\"","")
                    this.src = `${this.$host}${this.data.zhaoPianPath}?token=${this.tokenstr}`
                  }else{
                    this.src = this.noPic
                  }

                  this.$nextTick(function(){
                    //
                      let a = this.$refs.imgBox.offsetHeight
                      let b = this.$refs.imgMsg.offsetHeight
                      let c = this.$refs.leftcontent.offsetHeight
                      let temp = c-b-a -10
                      this.height = temp + 'px'
                  })


                }else{
                  this.$message.error(data.msg);
                }

              });
            },
            selectMenu(item){
              this.$emit('switchComponent',item)
            },
            selectPicture(){
              //
              this.$refs.selectPicture.initDialog()
            },
        }
    }
</script>

<style lang="scss" scoped type="text/stylus">
  /**/
  .divPhotoWithDesc{
    margin: 5px 0;
    .img-box{
      text-align: center;
      cursor: pointer;
      img{
        width: 120px;
        height:150px;
        border: 1px solid #c4c4c4;
        background-color: #fff;
      }
    }
    .img-msg{
      text-indent: 2em;
      line-height: 18px;
      padding: 6px;
    }
  }



  /**/
  .el-submenu .el-menu-item{
    height: 32px;
    line-height: 28px;
  }
  .popup-main-level{
    height:342px;
    overflow:auto;
  }
  .el-menu{
    border-right:none;
  }
  .el-menu-item, >>> .el-submenu__title{
    height: 25px;
    line-height: 25px;
    font-size: 15px;
    font-family: "Microsoft Yahei";
  }
  .level-list{
    display: inline-block;
    width: 8px;
    height: 8px;
    margin-right: 10px;
    background: url('../../../../assets/img/icon2.png') no-repeat -216px -160px;
  }
  .secondary-list{
    font-weight: 100;
    display: inline-block;
    width: 6px;
    height: 6px;
    margin-right: 10px;
    background: url('../../../../assets/img/icon2.png') no-repeat -216px -184px;
  }
  .el-menu-item.is-active {
    background: #EAF4FE!important;
    .secondary-list{
      font-weight: bold;
      background: url('../../../../assets/img/icon2.png') no-repeat -200px -184px;
    }
  }

  >>>.el-menu .el-submenu  .el-submenu__title{
      font-weight: bold;
  }
  >>>.el-submenu .el-menu-item{
    padding-left:22px!important;
  }






</style>



